.history{
  &__items{
    @include grid100gap(.375em);
  }
}

.history-item{
  display: flex;
  align-items: center;
  padding: .75em 1.25em;
  background-color: $dark;
  border-radius: .75em;
  min-height: 2.75em;
  &__title{
    max-width: calc(100% - 4.5em);
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  &__share{
    margin: 0 1.25em 0 .75em;
    width: 1.25em;
  }
  &__status{
    width: 1.25em;
    margin-left: auto;
  }
}
