.referal-page{
  background-image: url(../img/lines.svg);
  background-size: 65.8125em auto;
  background-repeat: no-repeat;
  background-position: center top;
}

.referal{
  padding: 4.5em 0 2em;
  position: relative;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__content{
    @include grid100gap(1.5em);
  }
  &__title{
    font-weight: 600;
    @include grid100gap(.3125em);
    &-big{
      font-size: 3.5em;
      line-height: 121%;
    }
    &-small{
      font-size: 2.25em;
      line-height: 122%;
    }
  }
  &__desc{
    max-width: 28em;
  }
  &__img{
    width: 26.0625em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.ellipse{
  pointer-events: none;
  position: absolute;
  bottom: 3.875em;
  width: 53.625em;
  height: 21.75em;
  left: 50%;
  transform: translateX(-60%);
  filter: blur(150px);
  border-radius: 53.625em;
  background: linear-gradient(91deg, rgba(72, 123, 255, 0.20) 23.13%, rgba(70, 39, 255, 0.20) 104.83%);
}

.how2{
  @media screen and (min-width: 1025px){
    .catalog__grid{
      width: calc(100% + .625em);
    }
    .catalog-card{
      width: calc(100% / 3 - .625em);
      margin: .625em .625em 0 0;
    }
  }
}

.how-card{
  display: flex;
  flex-direction: column;
  .catalog-card__text{
    margin: 0;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1em;
  }
}

.referal-link{
  padding: 2em 1.5em;
  border-radius: 1.5em;
  background: $blue;
  width: 28.375em;
  flex-shrink: 0;
  &__grid{
    margin-top: .875em;
    display: grid;
    grid-template-columns: calc(100% - 3.5em) 3em;
    gap: .5em;
  }
  &__wrap{
    height: 3em;
    display: flex;
    align-items: center;
    border-radius: 0.5em;
    border: 1px solid rgba(255, 255, 255, 0.40);
    padding: .5em .75em .5em 1em;
    position: relative;
  }
  &__text{
    width: 15em;
    max-width: calc(100% - 3em);
    .text16{
      @include truncate(1);
    }
  }
  &__copy{
    position: absolute;
    top: 50%;
    right: .75em;
    transform: translateY(-50%);
    width: 2em;
    &:hover{
      opacity: .8;
    }
    img{
      width: 100%;
      display: block;
    }
  }
  &__share{
    height: 3em;
    width: 100%;
    img{
      width: 2em;
    }
  }
}

.referal-list{
  padding: 6.75em 0 1.875em;
  &__grid{
    margin-top: .5em;
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 2em / 5));
    gap: .5em;
  }
}

.referal-card{
  padding: 1em;
  border-radius: 0.5em;
  background: $dark;
  position: relative;
  &._blue{
    background: $blue;
    grid-column: span 2;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url(../img/referal-bg.svg);
      background-size: cover;
      background-position: center;
    }
  }
  &__text{
    @include grid100gap(.5em);
  }
  &__nav{
    display: flex;
    margin-top: .875em;
    position: relative;
    z-index: 2;
  }
  &__link{
    color: inherit;
    font-size: 1em;
    line-height: 120%;
    text-decoration: underline;
  }
  &__top{
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 2;
  }
  &__btn{
    width: 7.5em;
    height: 2em;
  }
}

.referal-list2{
  padding: 1.875em 0;
  .search-field{
    margin-top: 1em;
  }
  .m-table,
  .empty-data{
    margin-top: .5em;
  }
}

.search-field{
  position: relative;
  &__icon{
    position: absolute;
    left: 1em;
    top: 50%;
    transform: translateY(-50%);
    width: 1.75em;
    height: auto;
  }
  input{
    width: 100%;
    height: 3em;
    font-size: 1em;
    line-height: 120%;
    font-weight: 500;
    border-radius: 0.5em;
    border: 1px solid rgba(152, 161, 192, 0.10);
    background: $dark;
    padding: 0 1em 0 3.75em;
    color: #fff;
    &::placeholder{
      color: $gray;
    }
    &:focus{
      border-color: rgba(152, 161, 192, 0.60);
      &~.search-field__icon{
        path{
          stroke: #fff;
        }
      }
    }
  }
  &._active{
    .select-dropdown{
      opacity: 1;
      pointer-events: unset;
      padding: .25em .25em 1.875em .75em;
      .m-table{
        margin: 0;
        padding: 0 .5em 0 0;
        max-height: 25em;
        overflow-y: auto;
        @include scroll;
      }
    }
  }
}

.empty-data{
  border-radius: 0.5em;
  min-height: 18.875em;
  background-color: $dark;
  @include center;
  &__inner{
    max-width: 30em;
    border-radius: 0.75em;
    border: 1px solid rgba(152, 161, 192, 0.20);
    padding: 1.5em 2em;
    text-align: center;
    @include grid100gap(.5em);
    a{
      text-decoration: underline;
    }
  }
}

.m-table{
  position: relative;
  border-radius: 0.5em;
  background-color: $dark;
  padding: 0 1em 1.25em;
  table{
    width: 100%;
    border-spacing: 0;
  }
  th,
  td{
    text-align: center;
    &:first-child{
      text-align: left;
    }
  }
  th{
    padding: 1em 0;
    color: $gray;
    font-weight: 500;
    span{
      font-size: .875em;
      line-height: 122%;
      display: inline-flex;
    }
  }
  td{
    padding: .625em 0;
    height: 3.56em;
    border-top: 1px solid rgba(152, 161, 192, 0.10);
  }
  .text16{
    font-size: .875em;
  }
  &__action{
    display: inline-flex;
    align-items: center;
  }
  &__btn{
    border-radius: 0.25em;
    padding: 0 .75em;
    width: auto;
    min-width: 5em;
    height: 1.5em;
    @include lastM(0 .25em 0 0);
    span{
      font-size: .75em;
    }
  }
}

.m-price{
  display: inline-flex;
  align-items: center;
  white-space: nowrap;
  &__currency{
    margin-left: .5em;
    color: rgba(152, 161, 192, 1);
  }
}

.referal-table{
  th,td{
    text-align: left;
    @media screen and (min-width: 1025px){
      &:first-child{
        width: 14em;
      }
    }
  }
}


@media screen and (max-width: 1024px){
  .referal{
    padding: 1em 0 1.4em;
    &__content{
      margin-right: 6em;
      width: 37.25em;
      max-width: 100%;
      flex-shrink: 0;
      gap: 1.25em;
    }
    &__title{
      &-big{
        font-size: 4em;
      }
      &-small{
        font-size: 2.5em;
      }
    }
    &__nav{
      margin-top: .75em;
    }
    &__img{
      width: calc(100% - 41.625em);
      flex-shrink: 0;
    }
    &__btn{
      height: 3em;
    }
    @media screen and (min-width: 577px){
      &__img{
        height: 41.23544em;
        overflow: hidden;
        img{
          height: 100%;
          width: auto;
        }
      }
      &__desc{
        font-size: 1.25em;
      }
    }
  }

  .how2{
    padding-top: 1.4em;
  }

  .referal2{
    .referal__inner{
      display: block;
    }
  }

  .referal-link{
    margin: -2.11em 0 0 auto;
  }

  .referal-list{
    padding: 10em 0 1em;
  }

  .referal-list2{
    padding: 1em 0;
  }
}


@media screen and (max-width: 576px){
  .referal{
    padding: 8.5em 0 4em;
    text-align: center;
    margin-top: -4.5em;
    &__inner{
      display: block;
    }
    &__img{
      margin: 2.25em auto 0;
      width: 17.81em;
    }
    &__title{
      width: calc(100% + 1.5em);
      margin-left: -.75em;
      display: block;
      &-big{
        color: $linkHover;
      }
      &-big,
      &-small{
        font-size: 2.75em;
        line-height: 110%;
        display: inline;
        font-weight: 700;
      }
    }
    &__nav{
      margin-top: .625em;
    }
    &__btn{
      width: 100%;
    }
  }

  .ellipse{
    bottom: unset;
    top: 15.5em;
  }

  .how2{
    padding: 4em 0;
    display: block;
  }

  .referal-link{
    text-align: left;
    margin: 4.5em 0 0;
    width: 100%;
    padding: 1.5em 1em;
    border-radius: 0.75em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__share{
      display: none;
    }
  }

  .referal-list{
    padding: 4.06em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .35em));
      gap: .7em;
      margin-top: 1.5em;
    }
    ._mob-big{
      grid-column: 1/-1;
    }
  }

  .referal-card{
    &._blue{
      padding-bottom: 4.625em;
    }
    &__nav{
      position: absolute;
      top: 1em;
      right: 1em;
      margin: 0;
    }
    &__top{
      position: static;
    }
    &__btn{
      position: absolute;
      bottom: 1em;
      left: 1em;
      width: calc(100% - 2em);
      height: 2.75em;
    }
  }

  .referal-list2{
    padding: 4.06em 0;
    .search-field{
      margin-top: .75em;
    }
  }

  .empty-data{
    &__inner{
      width: 16em;
      max-width: 100%;
    }
  }

  .mob-scroll-table{
    overflow-x: auto;
    padding: 0 .75em 1.25em;
    table{
      min-width: 59em;
    }
    th{
      padding: .75em 0;
    }
    td{
      padding: .65em 0;
      height: auto;
      border: none;
    }
  }
}
