.m-select{
  height: 3em;
  position: relative;
  &__field{
    height: 100%;
    position: relative;
    cursor: pointer;
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      right: 1em;
      transform: translateY(-50%);
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
      width: 2em;
      height: 2em;
    }
    input{
      color: #fff;
      pointer-events: none;
      width: 100%;
      height: 100%;
      font-size: 1em;
      line-height: 120%;
      font-weight: 500;
      padding: 0 4em 0 1em;
      border-radius: 0.5em;
      border: 1px solid rgba(152, 161, 192, 0.10);
      background: $dark;
      &::placeholder{
        color: $gray;
      }
    }
  }
  &__option{
    input{
      &:checked ~ *{
        color: $linkHover;
      }
    }
  }
  &._active{
    .select-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
    .nft-select__field:before{
      transform: rotate(-180deg) translateY(50%);
    }
  }
}

.nft-select{
  width: 21.125em;
  margin-left: .5em;
  &__head{
    display: flex;
    justify-content: space-between;
    margin-top: .75em;
  }
  &__items{
    margin-top: .75em;
    @include grid100gap(.75em);
  }
}

.select-dropdown{
  position: absolute;
  top: 100%;
  left: 0;
  margin-top: .5em;
  padding: 1em 1em 2.75em;
  width: 100%;
  @include darkBg;
  z-index: 5;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  .empty-data__inner{
    margin-top: 1.5em;
  }
}

.nft-item{
  position: relative;
  input{
    &:checked+.nft-item__inner{
      color: $linkHover;
    }
  }
  &__inner{
    min-height: 2em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.countdown{
  width: 13.9375em;
  height: 3em;
  flex-shrink: 0;
  margin-left: .5em;
  @include darkBg;
  padding: .5em;
  display: flex;
  align-items: center;
  img{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .5em;
  }
  .tooltip{
    width: 7.1875em;
    text-align: center;
  }
}

.tooltip{
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 1em;
  border-radius: .5em;
  border: 1px solid rgba(152, 161, 192, 0.20);
  background: #272A3C;
  padding: .75em;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  .triangle{
    top: 100%;
    left: 50%;
    width: 1em;
    height: 1em;
    transform: translate(-.625em,-50%);
    span{
      border-color: rgba(152, 161, 192, 0.20);
      background: #272A3C;
      transform: rotate(-45deg);
    }
  }
  &__wrap{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      height: 2em;
      width: 100%;
      bottom: 99%;
      left: 0;
    }
    &:hover{
      .tooltip{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &._show{
    opacity: 1;
    pointer-events: unset;
  }
}

.vote{
  .pair__content{
    display: block;
  }
  th,
  td{
    &:last-child{
      text-align: right;
      width: 3.4em;
      .mob-table__desc{
        display: block;
      }
    }
  }
  .m-table{
    @media screen and (min-width: 1025px){
      th span,
      .text16{
        font-size: .875em;
      }
      .tooltip .text16{
        font-size: 1em;
      }
    }
  }
  .mob-table{
    &__desc{
      display: inline-block;
    }
  }
}

.range-slider{
  width: 100%;
  height: .125em;
  border-radius: 6.25em;
  background: rgba($gray,.2);
  border: none;
  box-shadow: none;
}

.noUi-connect{
  background: $linkHover;
}

.noUi-handle{
  box-shadow: none;
  border: none;
  width: .75em !important;
  height: .75em !important;
  border-radius: 50%;
  background: #fff;
  right: -.3125em !important;
  top: -.3125em !important;
  cursor: pointer;
  filter: drop-shadow(-3px 2px 4px rgba(0, 0, 0, 0.15));
  &:hover{
    transform: scale(1.1);
  }
  &:before,
  &:after{
    display: none;
  }
}

.vote-tvl{
  @include lastM(0 0 1em);
  &__item{
    display: flex;
    justify-content: space-between;
    .text16{
      white-space: nowrap;
      font-size: 1em;
      @include lastM(0 .625em 0 0);
    }
  }
}

.vote-hint{
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2em;
  padding: 1.25em;
  min-width: 23.25em;
  @include darkBg;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__title{
    display: flex;
    align-items: center;
    margin-right: 2.5em;
    .text16{
      margin-right: .75em;
    }
  }
  &__change{
    color: #4AFF7D;
    &._red{
      color: $red;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  &__btn{
    min-width: 5em;
    padding: 0 .53em;
    width: auto;
    height: 1.5em;
    border-radius: .25em;
    @include lastM(0 .25em 0 0);
    span{
      font-size: .75em;
    }
  }
}

.vote-modal{
  .modal__dialog{
    width: 24.625em;
  }
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:not(:last-child){
      padding-bottom: 1.5em;
      border-bottom: 1px solid rgba(#fff,.2);
    }
    .vote-hint__title,
    .vote-hint__nav{
      margin-top: 0;
    }
    .vote-hint__title{
      margin-right: .5em;
    }
  }
}


@media screen and (min-width: 1025px){
  ._hide-desktop{
    display: none;
  }
}


@media screen and (max-width: 1024px){
  .countdown{
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 0 1em;
    img{
      display: none;
    }
    .tooltip{
      opacity: 1;
      pointer-events: unset;
      background-color: transparent;
      border: none;
      width: auto;
      transform: unset;
      margin: 0;
      position: static;
      padding: 0;
      .triangle{
        display: none;
      }
    }
  }

  .vote-page{
    &__title{
      margin-bottom: .67em;
    }
  }
}


@media screen and (max-width: 576px){
  .vote-nav{
    display: grid;
    grid-template-columns: calc(100% - 3.5em) 3em;
    gap: .5em;
    .nft-select,
    .search-field{
      grid-column: 1/-1;
      margin: 0;
      width: 100%;
      order: -1;
    }
    .countdown{
      width: 100%;
      margin: 0;
    }
    .filter-item{
      margin: 0;
      width: 100%;
    }
  }

  .vote-table{
    td{
      &:first-child,
      &:last-child{
        grid-column: 1/-1;
        width: 100%;
      }
    }
    .mob-table__desc{
      display: flex;
      width: 100%;
      &>.text16{
        @include lastM(0 .125em 0 0);
      }
    }
    .tooltip{
      display: none;
    }
  }

  ._range-block{
    height: 1.75em;
    display: flex;
    align-items: center;
    width: 100%;
  }

  .range-slider{
    height: 0.5em;
  }

  .noUi-handle{
    width: 1.75em !important;
    height: 1.75em !important;
    top: -.625em !important;
    right: -.3125em !important;
  }

  .vote-hint{
    bottom: 1.25em;
    width: 100%;
    min-width: unset;
    flex-wrap: wrap;
    padding-top: .5em;
    &__title,
    &__nav{
      margin-top: .75em;
    }
  }

  ._hide-mob{
    display: none;
  }

  .vote-page{
    &__title{
      margin-bottom: .5em;
    }
  }
}
