.rewards-nav{
  margin-top: 1em;
}

.reward-select{
  .select-dropdown{
    padding: .5em 1em;
  }
  .vest-table{
    padding: 0;
    tr{
      position: relative;
      &:first-child{
        td{
          border-top: none;
        }
      }

    }
    td{
      &:first-child{
        width: unset;
      }
      &:last-child{
        width: unset;
      }
      &:nth-child(2){
        text-align: center;
      }
    }
  }
}


.poll{
  margin-top: 1.5em;
}

.pool-table{
  margin-top: 1em;
  .pair{
    &__content{
      display: block;
    }
  }
  th,
  td{
    &:nth-child(1){
      width: 16em;
    }
    &:last-child{
      text-align: right;
    }
  }
}


@media screen and (max-width: 576px){
  .pool-table{
    th{
      &:nth-child(2){
        text-align: center !important;
        span{
          justify-content: center;
        }
      }
    }
    th,td {
      &:nth-child(1){
        min-width: 7em;
      }
      &:nth-child(2) {
        width: 5.5em;
      }
      &:nth-child(3){
        width: 4em;
        text-align: right;
      }
      &:nth-child(4){
        display: none;
      }
    }
  }
}
