.opportunities2{
  padding: 7.5em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
    gap: .5em;
    margin-top: 2.25em;
  }
}

.opportunity-card{
  background: radial-gradient(114.2% 133.85% at 7.14% 3.6%, rgb(35, 35, 54) 0%, rgb(35, 35, 54) 0.009999999747378752%, rgb(35, 35, 54) 100%);
  &:after{
    display: none;
  }
  &__icon{
    height: 2.75em;
    box-shadow: 0 0 25px -1px rgba(94, 100, 255, 0.4);
    border-radius: .75em;
  }
  &__text{
    margin-top: 2.75em;
    @include grid100gap(.875em);
  }
}

.burning{
  padding: 7.5em 0;
  &__grid{
    display: grid;
    grid-template-columns: 23.4375em calc(100% - 23.4375em - 2.8125em);
    gap: 1em 2.8125em;
  }
  &__content{
    padding-top: .75em;
  }
  &__info{
    @include grid100gap(1.25em);
    max-width: 41.25em;
  }
  &__items{
    margin-top: 3.25em;
    @include grid100gap(1em);
  }
}

.burning-sum{
  border: 1px solid rgba(152, 161, 192, 0.7);
  border-radius: .75em;
  background: rgba(32, 39, 57, 0.5);
  padding: 2em 2.25em;
  &__label{
    margin: 2.625em 0 .375em;
    @include center;
    &-icon{
      width: .9375em;
      height: .9375em;
      border-radius: .25em;
      background-color: $primary;
      margin-right: .5em;
    }
  }
  &__btn{
    margin-top: 1.5em;
    width: 100%;
  }
}

.burning-chart{
  position: relative;
  &__img{
    width: 100%;
    display: block;
  }
  &__content{
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    width: 100%;
    text-align: center;
    @include grid100gap(.375em);
    position: absolute;
  }
}

.burning-item{
  padding: 1.25em 1.5em;
  border: 1px solid rgba(115, 123, 149, 0.5);
  border-radius: .75em;
  display: flex;
  align-items: center;
  &__icon{
    box-shadow: 0 0 1.5625em -1px rgba(94, 100, 255, 0.4);
    width: 2.75em;
    margin-right: 1.5em;
  }
  &__text{
    @include grid100gap(.875em);
  }
}


@media screen and (max-width: 1024px){
  .opportunities2{
    padding: 4.75em 0 3.75em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .25em));
      margin-top: 1.875em;
    }
  }

  .burning{
    padding: 4.375em 0 4.75em;
    &__items{
      margin-top: 1.75em;
    }
  }
}

@media screen and (max-width: 576px){
  .opportunities2{
    padding: 4em 0 5em;
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1em;
    }
  }

  .opportunity-card{
    &__text{
      margin-top: 1.5em;
    }
  }

  .burning{
    padding: 4em 0;
    &__grid{
      grid-template-columns: 100%;
    }
    &__content{
      padding: 0;
    }
    &__info{
      gap: 1em;
    }
    &__items{
      margin-top: 1em;
      gap: .375em;
    }
  }

  .burning-sum{
    order: 2;
    padding: 1.5em 1.5em 2em;
  }

  .burning-item{
    align-items: flex-start;
    padding: .875em .75em;
    &__icon{
      width: 2.25em;
      margin-right: .75em;
    }
    &__text{
      gap: .3125em;
    }
  }
}
