.wallet-modal{
  &__top{
    margin-top: .75em;
    @include grid100gap(.75em);
  }
  .modal__dialog{

  }
}

.wallets{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .375em));
  gap: .5em .75em;
}

.wallet-item{
  @include darkBg;
  padding: 1em;
  height: 4em;
  display: flex;
  align-items: center;
  transition: all .3s;
  &:hover{
    cursor: pointer;
    background: rgba($linkHover,.2);
    border-color: $linkHover;
  }
  img{
    width: 2em;
    flex-shrink: 0;
    margin-right: .5em;
  }
  span{
    font-size: 1em;
    line-height: 120%;
  }
}


.wallets2{
  padding: 1.25em .75em;
  background-color: $dark;
  border-radius: 0.75em;
  .search-field{
    margin-bottom: .625em;
  }
}

.wallet-item2{
  display: flex;
  align-items: center;
  padding: .625em .75em;
  width: calc(100% + 1.5em);
  margin-left: -.75em;
  transition: all .3s;
  &:hover{
    cursor: pointer;
    background: rgba(152, 161, 192, 0.05);
  }
  img{
    width: 2em;
    flex-shrink: 0;
    margin-right: .5em;
  }
  span{
    font-size: 1em;
    line-height: 119%;
  }
}

.wallet{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: $dark;
  border-radius: .75em;
  height: 4.625em;
  padding: .75em;
  &__content{
    display: flex;
    align-items: center;
    img{
      width: 2em;
      flex-shrink: 0;
      margin-right: .75em;
    }
  }
  &__copy{
    width: 2em;
    img{
      display: block;
      width: 100%;
    }
  }
}

.wallet-type{
  background-color: $dark;
  border-radius: 0.75em;
  padding: .75em .75em .75em 1.25em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__content{
    display: flex;
    align-items: center;
    img{
      width: 1.32781em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__btn{
    width: auto;
    padding: 0 .75em;
    height: 1.5em;
    border-radius: 0.25em;
    span{
      font-size: .75em;
    }
  }
}

.wallet-modal2{
  .modal__dialog{
    gap: .75em;
  }
  .bribe-modal__buttons{
    padding: 0;
    border: none;
  }
  .bribe-modal__btn{
    justify-content: center;
    color: $gray;
    img{
      width: 1em;
      margin-left: .75em;
    }
    span{
      font-size: .75em;
      max-width: 100%;
    }
  }
}

.loader{
  width: 2.75em;
  margin: 0 auto;
  svg{
    width: 100%;
    height: auto;
    transition: all .3s;
  }
  circle{
    transition: all .3s;
  }
}

.circle-chart{
  transform: rotate(-85deg);
}

.wallet-modal3{
  .modal__dialog{
    padding-bottom: 3.38em;
  }
}

.opening-wallet{
  &__img{
    width: 2.12481em;
    display: block;
    margin: 0 auto;
  }
  &__text{
    margin-top: 1.5em;
    text-align: center;
    @include grid100gap(.5em);
  }
}

.alert{
  z-index: 100;
  position: fixed;
  bottom: 3.56em;
  left: 50%;
  transform: translateX(-50%);
  width: 19.8125em;
  max-width: calc(100% - 1.5em);
  padding: 1.69em 1em;
  border-radius: .75em;
  background-color: $red;
  text-align: left;
  &__close{
    width: 1em;
    display: block;
    position: absolute;
    top: .3125em;
    right: .3125em;
    svg{
      width: 100%;
      height: auto;
    }
    &:not(:hover){
      opacity: .5;
    }
  }
  &__inner{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 2em;
    flex-shrink: 0;
    margin-right: .25em;
    display: block;
  }
  &__text{
    @include grid100gap(.25em);
  }
}


@media screen and (max-width: 576px){
  .alert{
    padding: .75em;
    width: 100%;
  }
}
