.fg {
  position: relative;
  margin: 0;
  input {
    border: 1px solid $dark;
    padding: 0 .5em;
    height: 2.1875em;
    width: 100%;
    font-size: 1em;
    background-color: $dark;
    color: #fff;
    border-radius: .5em;
    font-family: $font1;
    &::placeholder {
      color: #2C3143;
    }
    &:focus {
      border-color: $primary;
    }
  }
  &._error{
    input{
      border-color: $red;
      color: $red;
    }
  }
}

.token-item,
.token-item2,
.slippage__item,
.m-switch,
.m-select__option,
.m-radio{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-radio{
  display: flex;
  align-items: center;
  font-weight: 300;
  color: $gray;
  transition: all .3s;
  &:hover{
    opacity: .8;
  }
  &__icon{
    margin-right: .5em;
    flex-shrink: 0;
    width: 1.25em;
    height: 1.25em;
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_220_35322)'%3E%3Cpath d='M10 2.5C16 2.5 17.5 4 17.5 10C17.5 16 16 17.5 10 17.5C4 17.5 2.5 16 2.5 10C2.5 4 4 2.5 10 2.5Z' stroke='%2398A1C0' stroke-width='1.33333' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_220_35322'%3E%3Crect width='20' height='20' fill='white'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E");
    @include bgImgSize(100%);
  }
  input{
    &:checked{
      &~.m-radio__icon{
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_220_35308)'%3E%3Cpath d='M10 2.5C16 2.5 17.5 4 17.5 10C17.5 16 16 17.5 10 17.5C4 17.5 2.5 16 2.5 10C2.5 4 4 2.5 10 2.5Z' stroke='url(%23paint0_linear_220_35308)' stroke-width='1.33333' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M10 5C14 5 15 6 15 10C15 14 14 15 10 15C6 15 5 14 5 10C5 6 6 5 10 5Z' fill='url(%23paint1_linear_220_35308)'/%3E%3C/g%3E%3Cdefs%3E%3ClinearGradient id='paint0_linear_220_35308' x1='6.05318' y1='2.50002' x2='18.5947' y2='2.80108' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='%23487BFF'/%3E%3Cstop offset='1' stop-color='%234627FF'/%3E%3C/linearGradient%3E%3ClinearGradient id='paint1_linear_220_35308' x1='7.36879' y1='5.00001' x2='15.7298' y2='5.20072' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='%23487BFF'/%3E%3Cstop offset='1' stop-color='%234627FF'/%3E%3C/linearGradient%3E%3CclipPath id='clip0_220_35308'%3E%3Crect width='20' height='20' fill='white'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E");
      }
    }
  }
}

.m-switch{
  input{
    &:checked+label{
      background: $blue;
      &:before{
        background-color: #fff;
        box-shadow: 0 .125em .5em 0 rgba(0, 0, 0, 0.16);
        transform: translateX(1em);
      }
    }
  }
  label{
    width: 2.25em;
    height: .875em;
    border-radius: 6.25em;
    background-color: rgba($gray,.2);
    transition: all .3s;
    display: flex;
    align-items: center;
    &:before{
      content: '';
      flex-shrink: 0;
      width: 1.25em;
      height: 1.25em;
      background-color: #fff;
      border-radius: 50%;
      transition: all .3s;
    }
  }
  &__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.check {

  input {

    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}
