@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.spoiler{
  &:not(._active){
    .spoiler__hidden{
      height: 0;
    }
  }
  &._active{
    .spoiler__arrow{
      transform: rotate(-180deg);
    }
  }
  &__toggle{
    cursor: pointer;
  }
  &__hidden{
    overflow: hidden;
    transition: all .5s;
  }
  &__arrow{
    margin-left: .25em;
    width: 1.25em;
  }
}

.farm-detail{
  margin-top: .75em;
  &__show{
    width: calc(100% + 3em);
    margin-left: -1.5em;
    padding: .75em 1.5em 0;
    border-top: 1px solid rgba(#2C3143,.4);
  }
  &__items{
    padding: .75em 0;
    @include grid100gap(.5em);
    img{
      width: 1.25em;
      margin-left: .25em;
    }
    .icon-btn{
      margin-left: .25em;
      img{
        margin: 0;
      }
    }
  }
}
