.footer{
  padding: 3.1em 0 2.75em;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 12.85em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__right{
    display: flex;
  }
  &__bottom{
    margin-top: 4.69em;
    padding-top: 1.125em;
    border-top: 1px solid rgba(#fff,.1);
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.footer-nav{
  display: flex;
  width: 25em;
  justify-content: space-between;
  @media screen and (min-width: 1025px){
    padding-right: 10em;
    position: relative;
    ._type2{
      position: absolute;
      top: 0;
      right: 0;
      width: 7.65em;
    }
  }
  &__group{
    display: flex;
    flex-direction: column;
    align-items: flex-start;

  }
  &__link{
    color: $gray;
    &:not(:first-child){
      margin-top: .75em;
    }
    &:hover{
      color: #fff;
    }
  }
}

.footer-community{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__link{
    margin-top: .75em;
    display: flex;
    align-items: center;
    color: $gray;
    &:hover{
      color: #fff;
      path{
        fill: #fff;
      }
    }
    ._view2{
      display: none;
    }
  }
  &__icon{
    width: 1.75em;
    height: 1.5625em;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    margin-right: .56em;
    svg{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.footer-offer{
  margin-left: 5.69em;
  flex-shrink: 0;
  width: 11.4375em;
  &__product,
  &__logo{
    border: 1px solid rgba($gray,.5);
    border-radius: .5em;
  }
  &__logo{
    padding: .5em .625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__product{
    padding: 1em .75em;
    margin-top: .5em;
  }
  &__info{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__currency{
    display: flex;
    align-items: center;
    img{
      margin-right: .25em;
      width: 1.5em;
      flex-shrink: 0;
    }
  }
  &__btn{
    margin-top: 1em;
    width: 100%;
    height: 1.5em;
    border-radius: .25em;
    span{
      font-size: .75em;
    }
  }
}


@media screen and (min-width: 1700px){
  .footer{
    padding-top: 6.44em;
  }
}


@media screen and (max-width: 1024px){
  .footer{
    padding-top: 3.95em;
    &__logo{
      width: 10em;
    }
    &__bottom{
      margin-top: 2.5em;
      padding-top: 1em;
    }
  }

  .footer-nav{
    width: 17.25em;
    &__group{
      &:not(:last-child){
        margin-bottom: 1.875em;
      }
    }
    @media screen and (min-width: 577px){
      &__title{
        font-size: 1.25em;
      }
      &__link{
        font-size: 1.25em;
        &:not(:first-child){
          margin-top: .6em;
        }
      }
    }
  }

  .footer-offer{
    margin-left: 4.81em;
  }

  .footer-community{
    @media screen and (min-width: 577px){
      .text16{
        font-size: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: 4.5em 0 2.125em;
    &__top{
      @include grid100gap(3em)
    }
    &__bottom{
      padding-top: 1.75em;
    }
  }


  .footer__right,
  .footer-nav{
    width: 100%;
    display: grid;
    grid-template-columns: calc(100% - 10.1625em - .69em) 10.1625em;
    gap: .69em;
  }

  .footer-nav{
    &__group{
      &:not(:last-child){
        margin-bottom: 0;
      }
    }
    ._type2{
      margin-left: calc(100% + .69em);
      margin-top: -1.75em;
      .footer-nav__link{
        width: 10.1625em;
      }
    }
  }

  .footer-offer{
    margin: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    &__logo{
      padding-left: .56em;
      padding-right: .56em;
    }
    &__product{
      padding-left: .69em;
      padding-right: .69em;
      order: -1;
      margin: 0 0 .75em;
    }
  }

  .footer-community{
    &__link{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}
