._ml8{
  margin-left: .5em;
}

._mt6{
  margin-top: .375em;
}

.swap-detail{
  padding: .75em 1em;
  border: 1px solid rgba($gray,.1);
  border-radius: .5em;
  &__show{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__title{
    margin-right: auto;
    padding-right: .25em;
  }
  &__count{
    img{
      width: 1.5em;
      margin-right: .25em;
    }
  }
  &__items{
    @include grid100gap(.75em);
  }
  &__item{
    display: flex;
    justify-content: space-between;
  }
  .spoiler__wrap{
    padding-top: 1.25em;
  }
}

.liquidity-settings{
  margin-left: 1.25em;
  position: relative;
  &._active{
    .liquidity-settings__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__btn{
    width: 1.75em;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        stroke: #487bff;
      }
    }
  }
  &__top,
  &__bg{
    display: none;
  }
  &__dropdown{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: 100%;
    margin-top: .125em;
    left: 50%;
    transform: translateX(-50%);
    filter: drop-shadow(4px 4px 16px rgba(0, 0, 0, 0.08));
    background: $bg;
    padding: 1em;
    border: 1px solid rgba($gray,.6);
    width: 18.5em;
    z-index: 2;
    border-radius: .75em;
    @include grid100gap(1.25em);
  }
  &__info{
    justify-content: space-between;
  }
  &__title{
    svg{
      margin-left: .25em;
      width: 1em;
    }
  }
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: .5em;
  }
  &__field{
    &-hint{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: .5em;
    }
  }
  &__mess{
    padding: .5em;
    background: linear-gradient(160deg, rgba(72, 123, 255, 0.4) 0%, rgba(70, 39, 255, 0.4) 100%);
    border-radius: .75em;
    display: flex;
    align-items: flex-start;
    img{
      width: 1.5em;
      margin-right: .5em;
    }
  }
  .spoiler__wrap{
    padding-top: 1.25em;
    @include grid100gap(1.25em);
  }
}

.settings-select{
  &__show{
    img{
      width: 1.25em;
      margin-left: .25em;
    }
  }
}

._line{
  height: 1px;
  width: 100%;
  background: rgba($gray,.1);
}


@media screen and (max-width: 1024px){

}

@media screen and (max-width: 576px){
  .swap-detail{
    padding: .75em;
  }

  .liquidity-settings{
    &._active{
      .liquidity-settings__bg{
        display: block;
      }
    }
    &__bg{
      z-index: 21;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $fullHeight;
      background: rgba($dark,.8);
    }
    &__dropdown{
      z-index: 25;
      top: unset;
      left: 0;
      transform: unset;
      bottom: 0;
      margin: 0;
      width: 100%;
      position: fixed;
      padding: 1.25em 1em 1em;
      border-radius: .75em .75em 0 0;
      border-bottom: none;
    }
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
}
