.farm-card{
  &__top{
    padding: 1em 1.5em 3em;
    border-radius: 1em 1em 0 0;
    background: $gradietn;
  }
  &__bottom{
    position: relative;
    z-index: 2;
    padding: 1.25em 1.5em;
    border-radius: 1em 1em 0 0;
    background-color: $dark;
    margin-top: -2em;
  }
  &__info{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
    align-items: flex-start;
    &._col2{
      grid-template-columns: repeat(2,calc(50% - .75em));
    }
    &-item{
      @include grid100gap(.25em);
      img{
        width: 1.25em;
        margin-left: .25em;
      }
      .icon-btn{
        margin-left: .25em;
        img{
          margin: 0;
        }
      }
    }
    &-top{
      min-height: 1.25em;
    }
  }
  &__btn{
    width: 100%;
    margin-top: 1.5em;
    height: 2.25em;
    img{
      width: 2em;
      margin-right: .5em;
    }
    ._small{
      width: 1.25em;
    }
    span{
      font-size: .875em;
    }
  }
  &__earn{
    &-info{
      @include grid100gap(.25em);
    }
    &-btn{
      border-width: 1px;
      width: auto;
      padding: 0 1em;
      height: 2.0625em;
      span{
        font-size: .875em;
      }
      img{
        width: 1.25em;
        margin-right: .625em;
      }
    }
    &-calc{
      display: flex;
      &-btn{
        margin-left: .375em;
        width: 2.25em;
        height: 2.25em;
        border-width: 1px;
        &:hover{
          background-color: rgba($primary,.3);
          path{
            stroke: #fff;
          }
        }
        svg{
          width: 1.25em;
        }
      }
    }
  }
  &__props{
    margin-top: 1.5em;
    @include grid100gap(.5em);
  }
  &__prop{
    display: flex;
    justify-content: space-between;
  }
}

.unstake-modal{
  &__nav{
    @include grid100gap(.75em);
    img{
      width: 2em;
      margin-right: .5em;
    }
  }
  &__nav2{
    display: flex;
    justify-content: center;
  }
  &__btn{
    @include center;
    color: #fff;
    img{
      width: 1.25em;
      margin-left: .5em;
    }
  }
}

.farm-merge{
  &__error{
    display: none;
    margin-top: .5em;
  }
  .pairs{
    margin-right: .75em;
    img{
      margin-right: 0;
    }
  }
  .withdraw-field._error {
    input {
      border-color: $red;
    }
    &~.farm-merge__error{
      display: block;
    }
  }
}

.roi-modal{
  &__desc{
    max-width: 23em;
  }
}

.roi-table{
  width: 100%;
  border-collapse: collapse;
  th,td{
    text-align: left;
    &:nth-child(1){
      @media screen and (min-width: 577px){
        width: 10em;
      }
    }
    &:last-child{
      text-align: right;
    }
  }
  th{
    font-weight: 500;
    color: $gray;
  }
  td{
    height: 3.125em;
  }
  tr{
    &:not(:first-child){
      td{
        border-top: 1px solid rgba($gray,.2);
      }
    }
  }
}


@media screen and (max-width: 576px){
  .roi-table {
    th, td {
      &:nth-child(1) {
        width: 8em;
      }
    }
  }
}
