.page-block{
  padding: 6.25em 0;
  &__inner{
    padding: 1.5em 1em;
    border: 1px solid rgba(152, 161, 192, 0.1);
    background: #0d111c;
    border-radius: 1em;
    width: 28.3125em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__links{
    display: flex;
    flex-grow: 1;
    border: 1px solid rgba(152, 161, 192, 0.1);
    background: #111524;
    padding: .375em .5em;
    border-radius: .5em;
  }
  &__link{
    @include center;
    height: 1.75em;
    flex-grow: 1;
    margin-right: 1.4375em;
    color: $gray;
    font-weight: 500;
    border-radius: .25em;
    &:last-child{
      margin-right: 0;
    }
    &:hover,
    &._active{
      background: $gradietn;
      color: #fff;
    }
  }
}

._mt24{
  margin-top: 1.5em;
}

.liquidity{
  &__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    width: 100%;
    height: 3em;
    img{
      width: 2em;
      margin-right: .5em;
    }
  }
}

.versions-nav{
  display: flex;
  align-items: center;
  padding: .25em;
  background: #111524;
  border-radius: .5em;
  &__link{
    @include center;
    height: 1.5em;
    @include lastM(0 .625em 0 0);
    &._disabled{
      color: rgba($gray,.6);
    }
    &._active{
      background: $gradietn;
      padding: 0 .25em;
      min-width: 1.5em;
      border-radius: .375em;
      &:hover{
        color: #fff;
      }
    }
  }
}

.your-liquidity{
  &__text{
    margin-top: 1.25em;
    @include grid100gap(.625em);
  }
  &__desc{
    a{
      text-decoration: underline;
    }
  }
  &__empty{
    color: $gray;
  }
}

.liquidity-earn{
  margin-top: 1.5em;
  padding: .75em 1em;
  border: 1px solid #1b1f2c;
  border-radius: .75em;
  display: flex;
  align-items: center;
  &__icon{
    border-radius: .5em;
    width: 2.5em;
    height: 2.5em;
    background: $gradietn;
    @include center;
    flex-shrink: 0;
    margin-right: .75em;
    img{
      width: 1.5em;
    }
  }
  &__content{
    @include grid100gap(.375em);
  }
  &__desc{
    color: $gray;
  }
}

.pairs{
  display: flex;
  align-items: center;
  img{
    width: 1.5em;
    &:nth-child(1){
      position: relative;
      z-index: 2;
    }
    &:nth-child(2){
      margin-left: -.625em;
    }
  }
}

.liquidity-select{
  margin-top: 1.25em;
  border-radius: .5em;
  background: #111524;
  &._active{
    .liquidity-select__arrow{
      transform: rotate(-180deg);
    }
  }
  &__show{
    display: flex;
    align-items: center;
    padding: 0 .75em;
    height: 2.5em;
    cursor: pointer;
  }
  &__icons{
    margin-right: .75em;
  }
  &__current{
    padding-right: .5em;
  }
  &__arrow{
    width: 1.25em;
    margin-left: auto;
    transition: all .3s;
  }
  &__hidden{
    padding: .75em .75em 1em;
  }
  &__props{
    @include grid100gap(.75em);
  }
  &__prop{
    display: flex;
    justify-content: space-between;
    &-desc{
      display: flex;
      align-items: center;
      img{
        width: 1.5em;
        margin-left: .5em;
        &:first-child{
          margin: 0 .5em 0 0;
        }
      }
    }
  }
  &__nav{
    margin-top: .75em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .75em;
  }
  &__btn{
    width: 100%;
  }
}


@media screen and (max-width: 1024px){
  .page-block{
    padding: 12.5em 0;
  }
}

@media screen and (max-width: 576px){
  .page-block{
    padding: 1em 0;
    &__inner{
      padding: 1em .75em;
    }
    &__link{
      margin-right: 0;
    }
  }

  .liquidity-settings{
    margin-left: .5em;
  }

  .liquidity-earn{
    padding: .75em;
    &__icon{
      width: 2em;
      height: 2em;
    }
  }

  .liquidity-select{
    margin-top: .75em;
  }
}
