.liquidity-add{
  .merge-fields{
    margin-top: 1.5em;
  }
  &__info{
    @include grid100gap(.375em);
    text-align: center;
  }
}

.liquidity-fields{
  .withdraw-field{
    input{
      padding-left: 5.71em;
    }
    &__buttons{
      left: 10em;
    }
  }
}

.liquidity-prices2{
  margin-top: 1.5em;
  &__items{
    margin-top: 1.25em;
    @include grid100gap(1em);
  }
  &__item{
    span{
      color: $gray;
    }
  }
}

.opacity-50{
  opacity: .5;
}

.opacity-30{
  opacity: .3;
}

.opacity-20{
  opacity: .2;
}

.modal{
  .liquidity-detail2{
    margin-top: 0;
  }
}

.promise-modal{
  &__btn{
    width: 100%;
  }
  &__info{
    padding: .75em 1.25em;
    border: 1px solid rgba($gray,.1);
    border-radius: .5em;
    @include grid100gap(.75em);
    &-wrap{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &-exchange{
      .pairs{
        margin-right: .75em;
      }
    }
  }
  &__item{
    justify-content: space-between;
    &-oil{
      img{
        width: 1.5em;
        margin-right: .25em;
      }
    }
  }
}

._flex,
._flex-between{
  display: flex;
  align-items: center;
}

._flex-between{
  justify-content: space-between;
}


@media screen and (max-width: 1024px){

}

@media screen and (max-width: 576px){
  .promise-modal{
    &__info{
      padding: .75em;
    }
  }

  .liquidity-fields{
    .withdraw-field{
      input{
        padding-left: 5em;
      }
      &__buttons{
        left: 8.75em;
        bottom: 1em;
      }
    }
  }
}
