.liquidity-remove{
  &__nav{
    margin-top: 1.5em;
    @include grid100gap(.75em);
    @media screen and (min-width: 577px){
      &._type2{
        grid-template-columns: repeat(2,calc(50% - .375em));
      }
    }
  }
  &__btn{
    height: 3em;
    width: 100%;
    img{
      width: 2em;
      margin-right: .5em;
    }
  }
}

._full{
  grid-column: 1/-1;
}

.progress{
  margin-top: 1.5em;
  border: 1px dashed rgba(152, 161, 192, 0.1);
  border-radius: .5em;
  padding: .75em;
  &._started{
    .progress__bar-fill,
    .progress__bar-fill2{
      opacity: 1;
    }
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 500;
  }
  &__title{
    color: $gray;
  }
  &__bottom{
    margin-top: .5625em;
    height: 1em;
    position: relative;
  }
  &__bar{
    position: absolute;
    background: #111524;
    height: .3125em;
    width: 100%;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    &-fill,
    &-fill2{
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      opacity: 0;
    }
    &-fill{
      height: 100%;
      background-image: url(../img/progress.svg);
      background-size: auto 100%;
      background-repeat: repeat-x;
      background-color: $primary;
    }
    &-round{
      width: 1.5625em;
      height: 1.5625em;
      border-radius: 50%;
      background-color: #fff;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      z-index: 5;
      position: absolute;
      border: .3125em solid $primary;
    }
    &-fill2{

    }
  }
  &__steps{
    display: flex;
    justify-content: space-between;
  }
  &__step{
    width: 1em;
    height: 1em;
    background-color: #111524;
    border: .1875em solid #1b1f2c;
    border-radius: 50%;
    position: relative;
    z-index: 2;
    &._done{
      background-color: #fff;
      border-color: $primary;
    }
  }
}

.liquidity-detail{
  margin-top: .75em;
  padding: .75em .75em 1em;
  background: #111524;
  border-radius: .5em;
  @include grid100gap(.75em);
  &__img{
    display: block;
    margin: .75em auto 0;
    width: 2.5em;
  }
  &__nav{
    display: flex;
    justify-content: flex-end;
  }
  &__btn{
    font-size: .75em;
  }
}

.liquidity-prices{
  margin-top: 1.5em;
  display: flex;
  justify-content: space-between;
}

.liquidity-detail2{
  margin-top: 1.5em;
  padding: .5em .75em;
  border: 1px solid rgba(152, 161, 192, 0.1);
  border-radius: .5em;
  &__top{
    display: flex;
    align-items: center;
  }
  &__icons{
    margin-right: .75em;
  }
  &__num{
    margin-left: auto;
  }
  &__bottom{
    margin-top: .75em;
    padding-top: .75em;
    border-top: 1px solid rgba($gray,.1);
    @include grid100gap(.75em);
  }
  &__item{
    span{
      color: $gray;
      margin-left: .25em;
    }
  }
  &__item2{
    display: flex;
    justify-content: space-between;
  }
}
