.m-alert{
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 1em;
  max-width: calc(100% - 2em);
  width: 16.125em;
  padding: .75em 1.625em .75em 1em;
  display: flex;
  align-items: center;
  background: linear-gradient(156deg, rgba(92, 255, 148, 0.4) 0%, rgba(0, 255, 163, 0.4) 100%);
  border-radius: .5em;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &--red{
    background: $red;
  }
  &--blue{
    background: linear-gradient(160deg, rgba(72, 123, 255, 0.4) 0%, rgba(70, 39, 255, 0.4) 100%);
  }
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__close{
    position: absolute;
    width: 1em;
    @include center;
    right: .3125em;
    top: .3125em;
    &:not(:hover){
      opacity: .5;
    }
    img{
      width: 100%;
    }
  }
  &__icon{
    width: 2em;
    margin-right: .25em;
    flex-shrink: 0;
  }
  .text12{
    margin-top: .33em;
  }
  .unstake-modal__btn{
    margin-top: .25em;
    img{
      width: 1em;
      margin-left: .25em;
    }
  }
}
