@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    font-weight: 500;
    width: 11.5625em;
    height: 2.5em;
    border-radius: 0.5em;
    max-width: 100%;
    &:disabled{
        opacity: .5;
        cursor: not-allowed;
    }
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 119%;
        letter-spacing: -0.01em;
    }
}

.m-btn-big{
    height: 3em;
    width: auto;
}

.m-btn-blue{
    color: #FFF;
    background: $blue;
    &:hover{
        color: #FFF;
        //background: linear-gradient(91deg, #6892FE 23.13%, #6850FF 104.83%);
        opacity: .8;
    }
}

.m-btn-red{
    color: #FFF;
    background: $red;
    &:hover{
        color: #FFF;
        background: #FF3F6D;
    }
}

.m-btn-green{
    color: #363636;
    background: #00FFA3;
    &:hover{
        color: #363636;
        background: darken(#00FFA3,10%);
    }
}

.m-btn-white{
    color: $linkHover;
    background: #FFF;
    &:hover{
        color: $linkHover;
        background: darken(#fff, 20%);
    }
}

.m-btn-white-outline{
    color: $textColor;
    border: 2px solid #fff;
    &:hover{
        color: $linkHover;
        background: #fff;
    }
}

.m-btn-blue-outline{
    color: $textColor;
    border: 2px solid $linkHover;
    &:hover{
        color: #FFF;
        background: $blue;
    }
}

.m-btn-green-outline{
    color: #00FFA3;
    border: 2px solid #00FFA3;
    &:hover{
        color: #363636;
        background: #00FFA3;
    }
}

.arrow-link{
    display: flex;
    align-items: center;
    &:hover{
        opacity: .8;
        img{
            transform: translateX(.25em);
        }
    }
    span{
        font-size: 1em;
        line-height: 119%;
    }
    img{
        transition: all .3s;
        width: .5em;
        margin-left: .75em;
    }
}

.m-btn-gray-outline{
    border: 1px solid rgba(255, 255, 255, 0.40);
    &:hover{
        border-color: #fff;
    }
}
