.error-404{
  overflow: hidden;
}

.error404{
  position: relative;
  background-image: url(../img/error-bg.webp);
  background-size: cover;
  background-position: right bottom;
  min-height: $fullHeight;
  &__inner{
    padding: 10em .75em;
    width: 57.75em;
    max-width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__num{
    width: 54em;
    max-width: 100%;
  }
  &__title{
    font-weight: 700;
    font-size: 4.0625em;
    line-height: 110%;
    margin-top: -1.5em;
  }
  &__nav{
    display: flex;
    margin-top: 6em;
  }
  &__btn{
    width: 11.5625em;
    height: 3em;
    border-radius: 0.5em;
  }
}


@media screen and (max-width: 1024px){
  .error404{
    background-image: url(../img/error-bg2.webp);
    background-position: 0 0;
    &__inner{
      width: 100%;
    }
    &__title{
      margin-top: .75em;
    }
    &__nav{
      margin-top: 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .error404{
    background-image: url(../img/error-bg3.webp);
    &__inner{
      padding: 9.25em .75em 2em;
      top: 0;
      bottom: unset;
      text-align: center;
    }
    &__title{
      font-size: 2.75em;
      margin-top: .75em;
    }
    &__nav{
      margin-top: 1.5em;
      width: 100%;
    }
    &__btn{
      width: 100%;
    }
  }
}
