.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    text-align: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    padding: 1em 0;
    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__bg {
        background: rgba($dark, 0.80);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        text-align: left;
        vertical-align: middle;
        border-radius: 1em;
        border: 1px solid rgba(152, 161, 192, 0.10);
        background: #0D111C;
        width: 28.3125em;
        max-width: calc(100% - 1.5em);
        //max-height: calc(100 * var(--vh));
        position: relative;
        z-index: 3;
        //overflow-y: auto;
        padding: 1.5em 1em;
        display: inline-grid;
        grid-template-columns: 100%;
        gap: 1.5em;
    }
    &__top{
        min-height: 2em;
        display: flex;
        align-items: center;
    }
    &__close {
        border-top: 0;
        background-color: transparent;
        width: 2em;
        margin-left: auto;
        display: flex;
        align-items: center;
        svg {
            width: 100%;
            height: auto;
        }
        &:not(:hover){
            opacity: 0.2;
        }
        &._back{
            order: -1;
            margin: 0 1em 0 0;
        }
    }
    &__buttons{
        @include grid100gap(.5em);
    }
    &__btn{
        height: 3em;
        width: 100%;
        img{
            width: 2em;
            margin-right: .5em;
            flex-shrink: 0;
        }
    }
    .empty-data__inner{
        width: 100%;
    }
}

.withdraw-field{
    height: 4.625em;
    position: relative;
    input{
        width: 100%;
        height: 100%;
        font-size: 1.75em;
        line-height: 121%;
        color: #fff;
        padding: .42em .42em 1em 4.57em;
        background-color: $dark;
        border: 1px solid $dark;
        border-radius: .42em;
        &::placeholder{
            color: $gray;
        }
        &:focus{
            border-color: $linkHover;
        }
    }
    &__token{
        position: absolute;
        left: .75em;
        top: 50%;
        transform: translateY(-50%);
        display: flex;
        align-items: center;
        font-weight: 500;
        &._regular-text{
            font-weight: 400;
        }
        img{
            width: 2em;
            flex-shrink: 0;
            margin-right: .75em;
            margin-left: .25em;
        }
        span{
            font-size: 1.25em;
            line-height: 120%;
        }
        .withdraw-field__token-arrow{
            width: 1.25em;
        }
        &-images{
            display: flex;
            flex-direction: row-reverse;
            img{
                margin: 0;
                &:not(:last-child){
                    margin-left: -.625em;
                }
            }
        }
        &-val{
            padding-left: .25em;
            margin-left: auto;
        }
    }
    &__buttons{
        position: absolute;
        bottom: .625em;
        left: 8em;
        display: flex;
        align-items: center;
        z-index: 2;
    }
    &__btn{
        display: flex;
        align-items: center;
        border-radius: 62.5em;
        border: 0.5px solid $gray;
        opacity: 0.3;
        margin-right: .25em;
        padding: 0 .5em;
        height: 1.25em;
        font-weight: 500;
        color: $gray;
        font-size: .75em;
        span{
            font-size: .625em;
            letter-spacing: 0.00375em;
        }
        &._active{
            background: $blue;
            border-color: $linkHover;
            color: #fff;
            opacity: 1;
        }
        &:hover{
            opacity: 1;
        }
    }
    &__hint{
        position: absolute;
        top: 50%;
        right: .75em;
        transform: translateY(-50%);
        @include grid100gap(.13em);
        text-align: right;
        .red-text{
            opacity: 0;
        }
    }
    &__select{
        position: absolute;
        width: 7.5em;
        height: 1.75em;
        left: .75em;
        top: 50%;
        transform: translateY(-50%);
        opacity: 0.5;
        font-weight: 500;
        border-radius: 6.25em;
        span{
            font-size: 0.75em;
        }
    }
    &__hint2{
        position: absolute;
        bottom: .5em;
        right: .75em;
    }
    &._type2{
        input{
            padding-top: .78em;
            padding-bottom: .78em;
        }
    }
    &._type3{
        .withdraw-field__token{
            width: calc(100% - 1.5em);
        }
    }
    &._type4{
        input{
            text-align: right;
            padding-bottom: .42em;
        }
    }
    &._pl160{
        input{
            padding-left: 5.7em;
        }
    }
    &._error{
        input{
            color: $red;
        }
        .withdraw-field__hint{
            .red-text{
                opacity: 1;
            }
        }
    }
    &__items{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .22em));
        gap: .44em;
    }
}

.withdraw-fee{
    @include grid100gap(1.25em);
    padding-bottom: 1.25em;
    border-bottom: 1px solid rgba(152, 161, 192, 0.10);
}

.welcome-modal{
    .modal__dialog{
        padding-top: 10.375em;
        border: none;
        &:before{
            content: '';
            position: absolute;
            width: 100%;
            height: 8.125em;
            background-image: url(../img/modal-bg.svg);
            background-size: cover;
            background-position: center;
            border-radius: 1em 1em 0 0;
        }
    }
}

.socials{
    display: flex;
    align-items: center;
    &__item{
        padding: 0;
        width: 3em;
        height: 3em;
        margin-right: .5em;
        flex-shrink: 0;
        img{
            display: block;
            max-width: 1.625em;
            max-height: 1.625em;
        }
    }
}

.bribe-modal{
    &._error{
        .modal__dialog{
            background: $red;
        }
    }
    .modal__dialog{
        background: $blue;
        gap: 1em;
    }
    .modal__close{
        &:not(:hover){
            opacity: 0.5;
        }
    }
    &__buttons{
        @include grid100gap(1em);
        padding-top: 1em;
        border-top: 1px solid rgba(#fff, .2);
    }
    &__btn{
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: space-between;
        text-align: left;
        font-size: 1em;
        line-height: 119%;
        img{
            width: 2em;
            flex-shrink: 0;
            margin-left: .5em;
        }
        &:hover{
            color: #fff;
            opacity: .7;
        }
    }
    &__socials{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .375em));
        gap: .75em;
        max-width: 23.875em;
        width: 100%;
        margin: 0 auto;
    }
    &__social{
        width: 100%;
        border-width: 1px;
        &:hover{
            background-color: transparent;
            opacity: .8;
            color: #fff;
        }
        svg{
            width: 1.25em;
            margin-right: .5em;
        }
    }
}

.bribe-modal2{
    .modal__close{
        top: .75em;
        right: 1em;
        position: absolute;
    }
    &__icon{
        margin: 0 auto;
        width: 6.25em;
        img{
            width: 100%;
            display: block;
        }
    }
    &__title,
    &__desc{
        text-align: center;
    }
    &__exchange{
        justify-content: center;
        &-item{
            img{
                width: 1.5em;
                margin-right: .75em;
            }
        }
        &-arrow{
            margin: 0 .75em;
            width: .75em;
        }
    }
    .bribe-modal__btn{
        justify-content: center;
        span{
            font-size: .75em;
        }
        img{
            width: 1.25em;
        }
    }
}



@media screen and (max-width: 1031px){
    .modal{
        &__dialog{

        }
    }
}


@media screen and (max-width: 700px){
    .modal{

    }

    .bribe-modal{
        &__btn{
            span{
                max-width: 60%;
            }
        }
    }

    .bribe-modal2{
        &__desc{
            max-width: 16em;
            margin: 0 auto;
        }
        .bribe-modal__btn{
            span{
                max-width: unset;
            }
        }
    }
}
