.swap{
  padding: 5.88em 0;
  .modal__dialog{
    position: static;
    margin: 0 auto;
    display: grid;
  }
}

.slippage{
  display: flex;
  flex-wrap: wrap;
  margin-top: -1.25em;
  width: calc(100% + 1.25em);
  &__item{
    margin: 1.25em 1.25em 0 0;
    position: relative;
    label{
      padding: 0 .75em;
      @include center;
      height: 2.1875em;
      border-radius: 0.5em;
      background-color: $dark;
      letter-spacing: 0.01em;
      font-size: 1em;
    }
    input{
      &:checked+label{
        background: $blue;
      }
    }
  }
}

.slippage-input{
  width: 6.375em;
  height: 2.1875em;
  margin: 1.25em 1.25em 0 0;
  position: relative;
  input{
    width: 100%;
    height: 100%;
    padding: 0 .75em;
    font-size: 1em;
    border-radius: 0.5em;
    background-color: $dark;
    border: 1px solid $dark;
    color: #fff;
    &:focus{
      border-color: $linkHover;
    }
    &::placeholder{
      color: $gray;
    }
  }
  label{
    font-size: 1em;
    color: rgba(152, 161, 192, 0.20);
    right: .5em;
    top: 50%;
    position: absolute;
    transform: translateY(-50%);
  }
}

.route{
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  &:after{
    content: '';
    @include centerAbsolute;
    background-image: url(../img/route.svg);
    width: 100%;
    height: 1.375em;
    background-size: auto 100%;
    background-position: center;
    background-repeat: no-repeat;
  }
  &__item{
    position: relative;
    z-index: 2;
    width: 2em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.price-impact{
  min-height: 2.4375em;
  border-radius: 0.75em;
  @include center;
  background: linear-gradient(91deg, rgba(92, 255, 148, 0.40) 0.63%, rgba(0, 255, 163, 0.40) 94.8%);
  padding: .75em 1em;
}

.exchange-rate{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: .53em 1.25em;
  border-radius: 0.5em;
  border: 1px solid rgba(152, 161, 192, 0.10);
  &__text{
    span{
      color: $gray;
    }
  }
  &__sale{
    display: flex;
    align-items: center;
    img{
      width: 1em;
      margin-right: .25em;
      flex-shrink: 0;
    }
  }
}

.token-modal{
  .modal__dialog{
    gap: 1.25em;
  }
}

.tokens{
  display: flex;
  flex-wrap: wrap;
  margin-top: -.25em;
  width: calc(100% + .25em);
  padding-bottom: 1.25em;
  border-bottom: 1px solid rgba(152, 161, 192, 0.10);
  .token-item{
    margin: .25em .25em 0 0;
  }
}

.token-item{
  input:checked,
  input:hover{
    &~.token-item__inner{
      border-color: $linkHover;
      background: linear-gradient(91deg, rgba(72, 123, 255, 0.30) 23.13%, rgba(70, 39, 255, 0.30) 104.83%);
    }
  }
  &__inner{
    transition: all .3s;
    display: flex;
    align-items: center;
    height: 2.5em;
    padding: 0 .5em;
    border-radius: 0.5em;
    border: 1px solid rgba(152, 161, 192, 0.10);
  }
  img{
    width: 1.5em;
    margin-right: .5em;
    flex-shrink: 0;
  }
  span{
    font-size: 1em;
    display: flex;
    align-items: center;
  }
}

.tokens2{
  margin-top: -.75em;
  @include grid100gap(.25em);
}

.token-item2{
  width: calc(100% + 2em);
  margin-left: -1em;
  input:checked,
  input:hover{
    &~.token-item2__inner{
      background: rgba(152, 161, 192, 0.05);
    }
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .5em 1em;
  }
  .pair{
    &__content{
      display: block;
    }
    &__img{
      width: 2em;
    }
  }
  &__nums{
    text-align: right;
  }
}


@media screen and (max-width: 992px){
  .swap{
    padding: 2em 0;
  }
}


@media screen and (max-width: 576px){
  .swap{
    padding: 1em 0;
    .modal__dialog{
      max-width: 100%;
    }
  }

  .slippage{
    overflow-x: auto;
    margin: 0 0 0 -1em;
    padding: 0 1em;
    flex-wrap: nowrap;
    width: calc(100% + 2em);
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
    &__item{
      margin: 0 .375em 0 0;
      flex-shrink: 0;
    }
  }

  .slippage-input{
    order: -1;
    margin: 0 .375em 0 0;
    flex-shrink: 0;
  }

  .price-info{
    .vechr__info{
      display: block;
    }
  }
}
