.token-page{
  background-image: url(../img/token-bg.svg);
}

.token-main{
  text-align: center;
  &__inner{
    padding: 11.25em 0 13.75em;
    position: relative;
  }
  &__text{
    @include grid100gap(.375em);
    width: 43.75em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__title{
    font-size: 4.25em;
    line-height: 121%;
    font-weight: 600;
  }
  &__nav{
    width: 23.625em;
    max-width: 100%;
    margin: 2.5em auto 0;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: .75em .5em;
  }
  &__btn{
    width: 100%;
  }
  &__coin{
    position: absolute;
    width: 14.625em;
  }
  ._coin1{
    left: 3.75em;
    top: -2em;
  }
  ._coin2{
    right: -3.125em;
    top: -2em;
    transform: rotate(-157deg);
    width: 17.625em;
  }
  ._coin3{
    right: 0;
    bottom: -3em;
  }
  ._coin4{
    left: 3em;
    bottom: -6em;
  }
}

.integrate{
  padding: 7.5em 0 10em;
  &__inner{
    padding: 2.625em 2.5em 3.125em 20em;
    border-radius: .75em;
    background: $gradietn;
    border: 1px solid rgba(255, 255, 255, 0.44);
    @include grid100gap(1.25em);
    position: relative;
  }
  &__img{
    position: absolute;
    top: -5.75em;
    left: 2em;
    width: 16.5em;
    transform: rotate(-37deg);
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 1024px){
  .token-page{
    background-image: url(../img/token-bg2.svg);
  }

  .token-main{
    &__inner{
      padding: 15em 0 16.875em;
    }
    &__text{
      width: 30em;
    }
    &__title{
      font-size: 3.5em;
    }
  }

  .integrate{
    padding-top: 3.75em;
    &__inner{
      padding: 1.75em 5.625em 1.5em 19.375em;
    }
    &__img{
      left: 1.5em;
    }
  }
}

@media screen and (max-width: 576px){
  .token-page{
    background-image: url(../img/token-bg3.svg);
  }

  .token-main{
    overflow: hidden;
    &__inner{
      padding: 11.25em 0 12.1875em;
    }
    &__text{
      gap: 1em;
    }
    &__title{
      font-size: 2.75em;
      line-height: 110%;
    }
    &__nav{
      margin-top: 1em;
      grid-template-columns: 100%;
    }
    &__btn{
      height: 3em;
    }
    ._coin1{
      left: -2em;
      top: 5em;
      width: 5.875em;
    }
    ._coin2{
      right: -1em;
      top: 0;
      width: 6.625em;
    }
    ._coin3{
      right: -2.125em;
      width: 12.25em;
    }
    ._coin4{
      bottom: 3em;
      width: 9em;
      left: -4.5em;
    }
  }

  .integrate{
    padding: 5em 0 10em;
    overflow: hidden;
    &__inner{
      padding: 1.5em 1.25em 7.5em;
      gap: 1.25em;
    }
    &__img{
      left: unset;
      top: unset;
      bottom: -12.5em;
      right: -.75em;
      transform: rotate(-86deg);
    }
  }
}
