@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.m-page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $textColor;
    font-size: calc(1440px * (16 / 1440));
    @media screen and (max-width: 1440px){
        font-size: calc(100vw * (16 / 1440));
    }
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
    }
    &.fixed-body2{
        padding-right: var(--scrollWidth);
    }
}

html{
    height: auto;
    background: linear-gradient(180deg, #202738 0%, #070816 47.6%);
}

.index-page{
    background-image: url(../img/page-bg.svg);
    background-size: cover;
    background-position: center top;
}

.container {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    padding: 0 9.5em;
}

a {
    &:hover {
        color: $linkHover;
    }
}


@media screen and (max-width: 1920px){
    .index-page{
        background-size: 120em auto,cover;
    }
}


@media screen and (max-width: 1024px){
    .m-page{
        font-size: calc(100vw * (16 / 1024));
    }

    .container{
        padding: 0 1.625em;
        &._mob-full{
            padding: 0;
        }
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 375));
    }

    .index-page{
        background-image: url(../img/page-bg-mob.svg);
        background-size: 23.4375em auto,cover;
    }

    .wrapper{
        overflow: hidden;
    }

    .container{
        padding: 0 .75em;
    }
}
