@import "../vars.scss";
.breadcrumbs {
    margin: 1em 0 1.5em;
    &__item {
        font-weight: 500;
        font-size: 1em;
        line-height: 120%;
        position: relative;
        display: inline;
        color: $gray;
        &:not(:first-child) {
            margin-left: 1.12em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: .375em;
                height: .6875em;
                background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='6' height='11' viewBox='0 0 6 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 9.5L5 5.5L1 1.5' stroke='%2398A1C0' stroke-width='1.33333' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
                @include bgImgSize(100%);
                top: .34em;
                right: 100%;
                margin-right: .4em;
            }
        }
        &:last-child{
            color: #fff;
            &:before{
                background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='6' height='11' viewBox='0 0 6 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 9.5L5 5.5L1 1.5' stroke='%23fff' stroke-width='1.33333' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
            }
            span{
                color: $gray;
            }
        }
    }
}
