.metric{
  padding: 13.75em 0 7.5em;
  @media screen and (min-width: 1025px){
    .container{
      padding: 0 2em;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 33.75em - .3125em) 33.75em;
    gap: 4.625em .3125em;
  }
  &__col{
    display: grid;
    grid-template-columns: 25.4375em calc(100% - 28.9375em);
    gap: 3.5em;
    align-items: center;
  }
  &__stat{
    display: grid;
    grid-template-columns: 50% 50%;
    gap: 1.75em 0;
    align-items: flex-start;
  }
}

.metric-chart{
  position: relative;
  &__img{
    width: 100%;
    display: block;
  }
  &__content{
    @include centerAbsolute;
    width: 100%;
    text-align: center;
    @include grid100gap(.3125em);
  }
}

.stat-item{
  @include grid100gap(.75em);
  &__line{
    height: .5em;
    width: 4.625em;
    border-radius: .75em .75em 0 0;
  }
  &__title{
    font-size: 1em;
    line-height: 130%;
    font-weight: 600;
  }
  &__desc{
    font-size: 1em;
    line-height: 150%;
    font-weight: 300;
  }
}

.metric-info{
  padding: 2em 1.5em;
  background: $gradietn;
  border-radius: .75em;
  &__items{
    @include grid100gap(1em);
    margin-top: 1.5em;
  }
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-coin,
    &-share{
      display: flex;
      align-items: center;
      img{
        width: 1.25em;
        margin-right: .25em;
      }
    }
    &-share{
      img{
        margin: 0 0 0 .25em;
      }
    }
  }
}


@media screen and (max-width: 1024px){
  .metric{
    padding: 16.875em 0 4.375em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__col{
      grid-template-columns: 25.4375em calc(100% - 25.4375em - 5.625em);
      gap: 2em 5.625em;
    }
    &__stat{
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 1.75em 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .metric{
    padding: 12.1875em 0 4em;
    &__grid{
      gap: 1.75em;
    }
    &__col{
      grid-template-columns: 100%;
    }
    &__stat{
      grid-template-columns: repeat(2,50%);
      gap: 1.75em 0;
    }
  }

  .metric-info{
    padding: 1.25em 1em;
  }
}
