.vest-page{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .tooltip{
      width: 10em;
      padding: .625em;
      &__wrap{
        margin-left: .5em;
      }
    }
  }
  &__title{
    display: flex;
    align-items: center;
    img{
      width: 1.5em;
      flex-shrink: 0;
    }
  }
  &__btn{
    img{
      width: 2em;
      margin-right: .5em;
    }
  }
}

.vest{
  margin-top: 1.25em;
  .vest-table,
  .empty-data{
    min-height: calc(100 * var(--vh) - 21.25em);
  }
}

.vest-table{
  th,td{
    &:nth-child(1){
      width: 16em;
    }
    &:nth-child(2){
      text-align: left;
    }
    &:nth-child(4),
    &:last-child{
      text-align: right;
    }
  }
  .pair__content{
    display: block;
  }
  ._hide{
    display: none;
  }
}

.lock-modal{
  .modal__dialog{
    gap: 1.125em;
  }
  &__content{
    @include grid100gap(1.125em);
  }
  .tabs{
    border-radius: 6.25em;
    background-color: $dark;
  }
  .tab{
    flex-grow: 1;
    border-radius: 6.25em;
    justify-content: center;
    span{
      font-size: .75em;
    }
  }
}

.lock-stat{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 .75em .5em;
  border-radius: 0.5em;
  border: 1px solid rgba(152, 161, 192, 0.10);
  flex-wrap: wrap;
  &__item{
    margin-top: .5em;
  }
}

.m-field{
  height: 4.625em;
  position: relative;
  input{
    padding-left: 2em;
    padding-right: 2em;
    width: 100%;
    height: 100%;
    font-size: 1.75em;
    color: #fff;
    border: none;
    border-radius: .42em;
    background-color: $dark;
    &::placeholder{
      color: $gray;
    }
  }
  label{
    font-size: 1em;
    line-height: 119%;
    font-weight: 400;
    color: $gray;
  }
  &__icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 2em;
    display: block;
    left: .75em;
  }
}

.date-field{
  z-index: 3;
  input{
    text-align: center;
  }
}

.air-datepicker{
  border-radius: 0.75em !important;
  border: 1px solid rgba(152, 161, 192, 0.20) !important;
  background: $dark;
  font-size: 1em !important;
  padding: 1.25em .75em;
  &--pointer{
    &:before,
    &:after{
      display: none;
    }
  }
  &--content{
    padding: 0;
  }
  &-cell{
    height: unset;
    font-size: 1em;
    line-height: 119%;
    font-weight: 500;
    color: #fff;
    &:hover{
      background-color: transparent !important;
      color: $linkHover;
    }
    &.-other-month-{
      color: rgba($gray,.5) !important;
    }
    &.-current-{
      color: #fff !important;
      position: relative;
      &:before{
        content: '';
        @include centerAbsolute;
        width: 1.8125em;
        height: 1.8125em;
        border-radius: 50%;
        background: $blue;
        z-index: -1;
      }
    }
  }
}

.air-datepicker-nav{
  padding: 0;
  margin-bottom: 1em;
  border: none;
  position: relative;
  &--title{
    order: -1;
    padding: 0;
    font-size: 1em;
    font-weight: 500;
    color: #fff;
  }
  &--action{
    position: absolute;
    right: .75em;
    top: 50%;
    transform: translateY(-50%);
    width: 1.5em;
    height: 1.5em;
    background-image: url(../img/next2.svg);
    @include bgImgSize(100%);
    &[data-action="prev"]{
      right: 2.75em;
      background-image: url(../img/prev2.svg);
    }
    svg{
      display: none;
    }
    &:hover{
      //background-color: transparent;
      //@include bgImgSize(100%);
      opacity: .8;
    }
  }
}

.air-datepicker-body{
  &--day{
    &-names{
      margin: 0 0 1.5em;
    }
    &-name{
      font-size: 1em;
      line-height: 119%;
      font-weight: 500;
      color: $gray;
      text-transform: capitalize;
    }
  }
  &--cells{
    grid-auto-rows: unset !important;
    gap: 1.5em 0;
    text-align: center;
  }
}

.lock-expires{
  &__grid{
    margin-top: .75em;
    width: calc(100% + .5em);
    display: flex;
    flex-wrap: wrap;
  }
  .m-radio{
    margin: .5em .5em 0 0;
    &__icon{
      order: 2;
      margin: 0 0 0 .25em;
      width: 2em;
      height: 2em;
    }
  }
}

.reward,
.vechr,
.vote-power{
  padding: 1.25em 0;
  @include grid100gap(1.25em);
  border-top: 1px solid rgba(152, 161, 192, 0.10);
  border-bottom: 1px solid rgba(152, 161, 192, 0.10);
  &__info{
    display: flex;
    .text16{
      margin-right: .625em;
    }
  }
}

.vote-power{
  &__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__info2{
    text-align: right;
  }
}

.reward{
  border-top: none;
  padding-top: 0;
  &__items{
    @include grid100gap(.625em);
  }
  &__item{
    color: rgba(152, 161, 192, 0.20);
    font-size: 1em;
    line-height: 119%;
    span{
      color: $gray;
    }
  }
}

.merge-fields{
  @include grid100gap(.5em);
  position: relative;
  &__arrow{
    position: absolute;
    left: 50%;
    top: 3.625em;
    transform: translateX(-50%);
    @include center;
    width: 2.5em;
    height: 2.5em;
    border-radius: 0.75em;
    border: .25em solid #0D111C;
    background: $dark;
    z-index: 3;
    img{
      width: 1em;
    }
  }
}

.merge-field{
  &._center{
    input{
      padding-top: 0;
    }
  }
  input{
    font-size: 1.25em;
    line-height: 120%;
    padding-top: 1em;
    padding-left: 2.8em;
    padding-right: 2.8em;
  }
  label{
    position: absolute;
    left: 3.5em;
    top: .97em;
  }
  &._small{
    input{
      font-size: 1em;
      padding-left: 3.5em;
      padding-right: .5em;
      padding-top: 1.25em;
      color: rgba(152, 161, 192, 0.20);
    }
  }
  &._reverse{
    label{
      top: unset;
      bottom: 1.12em;
    }
    input{
      padding-top: 1.12em;
      padding-bottom: 2.31em;
    }
  }
}

.hint{
  display: flex;
  align-items: flex-start;
  padding: .75em 1em;
  border-radius: 0.75em;
  background: linear-gradient(91deg, rgba(72, 123, 255, 0.40) 23.13%, rgba(70, 39, 255, 0.40) 104.83%);
  img{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .5em;
  }
}

.distribution__item,
.min-max{
  height: 2.6875em;
  border-radius: 0.75em;
  background-color: $dark;
  padding: .75em 1.25em;
  display: flex;
  justify-content: space-between;
}

.distribution{
  @include grid100gap(.25em);
  &__percent{
    width: 3em;
    text-align: right;
  }
  &__num{
    width: 2.5em;
  }
  &__total{
    @include darkBg;
    padding: .5em;
    @include center;
  }
}


@media screen and (max-width: 1024px){
  .vest-page{
    &__top{
      position: relative;
      padding-top: 4em;
    }
    &__title2{
      position: absolute;
      left: 0;
      top: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .vest-page{
    padding-bottom: 5.5em;
    &__top{
      padding: 0;
      justify-content: unset;
    }
    &__title2{
      position: static;
    }
    &__title{
      margin-left: .5em;
      .text16{
        color: $gray;
        line-height: 180%;
        &:before{
          content: ' / ';
        }
      }
    }
    &__nav{
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
      z-index: 3;
      @include darkBg;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      border-bottom: none;
      border-left: none;
      border-right: none;
      padding: 1em .75em;
    }
    &__btn{
      width: 100%;
      height: 3em;
    }
  }

  .vest{
    margin-top: .75em;
    .vest-table,
    .empty-data{
      min-height: calc(100 * var(--vh) - 13.25em);
    }
  }

  .pool-table,
  .vest-table {
    table{
      min-width: 100%;
    }
    th,td{
      &:nth-child(1) {
        min-width: 5em;
        width: unset !important;
      }
      &:nth-child(2) {
        text-align: left !important;
      }
    }
    ._hide{
      display: block;
    }
  }

  .vest-table{
    th,td{
      &:nth-child(3),
      &:nth-child(5){
        display: none;
      }
    }
    .pair{
      &__img{
        display: none;
      }
      &__content{
        margin: 0;
      }
    }
  }

  .vest-modal{
    &__buttons{
      @include grid100gap(.5em);
    }
    &__item{
      justify-content: space-between;
      display: flex;
      align-items: center;
      padding: 1.5em 0;
      border-top: 1px solid rgba(152, 161, 192, 0.10);
      border-bottom: 1px solid rgba(152, 161, 192, 0.10);
    }
    .pair{
      &__content{
        display: block;
      }
    }
  }
}
