$font1:'Inter',sans-serif;
$bg: linear-gradient(180deg, #202738 0%, #070816 100%);
$textColor: #fff;
$primary: #487BFF;
$dark: #111524;
$gray: #98A1C0;
$gray2: #C3C5CB;
$blue: linear-gradient(91deg, #487BFF 23.13%, #4627FF 104.83%);
$red: #FF1B51;
$green: linear-gradient(91deg, #5CFF94 0.63%, #00FFA3 94.8%);
$radio: radial-gradient(114.2% 133.85% at 7.14% 3.6%, rgba(35, 35, 54, 0.70) 0%, rgba(35, 35, 54, 0.70) 0.01%, rgba(35, 35, 54, 0.10) 100%);
$card: linear-gradient(138deg, rgba(255, 255, 255, 0.10) 0%, rgba(152, 161, 192, 0.07) 74.32%);
$cardHover: linear-gradient(138deg, rgba(72, 123, 255, 0.32) 0%, rgba(70, 62, 255, 0.24) 74.32%);
$linkHover: #487BFF;
$border: 1px solid rgba(255, 255, 255, 0.32);
$gradietn: linear-gradient(160deg, #487bff 0%, #4627ff 100%);

$bgWhite: linear-gradient(180deg, #E6E7FF 0%, #FFFCFE 100%);

$fullHeight: calc(100 * var(--vh));
