.text64{
  font-size: 4em;
  line-height: 110%;
}

.text56{
  font-size: 3.5em;
  line-height: 121%;
}

.text36{
  font-size: 2.25em;
  line-height: 110%;
}

.text32{
  font-size: 2em;
  line-height: 122%;
}

.text28{
  font-size: 1.75em;
  line-height: 122%;
}

.text24{
  font-size: 1.5em;
  line-height: 120%;
}

.m-list span,
.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.m-list2 li,
.text16{
  font-size: 1em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 125%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.light-text{
  font-weight: 300;
}

.medium-text{
  font-weight: 500;
}

.semi-bold-text{
  font-weight: 600;
}

.bold-text{
  font-weight: 700;
}

.gray-text{
  color: $gray;
}

.gray-text2{
  color: $gray2;
}

.gray-text3{
  color: rgba($gray, 0.20);
}

.gray-text4{
  color: rgba($gray, 0.50);
}

.primary-text{
  color: $primary;
}

.green-text{
  color: #00FFA3;
}

.white-text{
  color: #fff;
}

.linear-text,
.main__float-btn:not(:hover){
  color: $linkHover;
}

.red-text{
  color: $red;
}

.m-list{
  @include grid100gap(.25em);
  li{
    position: relative;
    padding-left: 1.66em;
    &:before{
      content: '';
      position: absolute;
      height: 100%;
      width: 1.66em;
      left: 0;
      top: 0;
      background-image: url(../img/list-arrow.svg);
      background-size: 1em auto;
      background-repeat: no-repeat;
      background-position: left center;
    }
  }
}

.text-center{
  text-align: center;
}

.underline-text{
  text-decoration: underline;
}


@media screen and (min-width: 1700px){
  .text48-big{
    font-size: 3em;
  }

  .text20-big{
    font-size: 1.25em;
  }
}


@media screen and (max-width: 576px){
  .text64,
  .text56{
    font-size: 2.75em;
  }

  .text20{
    font-size: 1em;
  }

  .text32-mob{
    font-size: 2em;
  }

  .text28-mob{
    font-size: 1.75em;
  }

  .text24-mob{
    font-size: 1.5em;
  }

  .text20-mob{
    font-size: 1.25em;
  }

  .text16-mob{
    font-size: 1em;
  }

  .text14-mob{
    font-size: .875em;
  }

  .text12-mob{
    font-size: .75em;
  }

  .semi-bold-text-mob{
    font-weight: 600;
  }
}
