.farm{
  padding: 2em 0 6em;
  &._card-view{
    .farm-table{
      display: none;
    }
    .farm-cards{
      display: grid;
    }
  }
}

.farm-cards{
  display: none;
  grid-template-columns: repeat(3,calc(100% / 3 - 1em));
  gap: 1.5em;
  margin-top: 1.875em;
}

.farm-main{
  display: grid;
  grid-template-columns: 28.875em 29.8125em;
  justify-content: space-between;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc,
  &__list{
    margin-top: .375em;
  }
  &__desc2{
    margin-top: 1.5em;
  }
}

.m-list2{
  padding-left: .5em;
  li{
    padding-left: 1em;
    position: relative;
    &:before{
      content: '•';
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.farm-filter{
  margin-top: 2em;
  &__grid{
    display: flex;
    margin-top: 1em;
  }
  .search-field{
    flex-grow: 1;
  }
  &__staked{
    flex-shrink: 0;
    margin-left: 1em;
    .m-switch{
      margin-left: 1em;
    }
  }
}

.view-toggle{
  margin-left: .75em;
  display: flex;
  &__item{
    margin-right: .3125em;
    width: 2.5em;
    height: 2.5em;
    border: 1px solid rgba($gray,.1);
    background-color: $dark;
    border-radius: .5em;
    @include center;
    &:last-child{
      margin-right: 0;
    }
    svg{
      width: 1.75em;
    }
    &:hover,
    &._active{
      path{
        stroke: $primary;
      }
    }
  }
}

.farm-table{
  margin-top: 1.75em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 46.5em) 10.625em 10.625em 10.625em 10.625em 4em;
    padding-left: 1.5em;
    padding-right: 1.5em;
  }
  &__head{
    margin-bottom: .25em;
  }
  &__items{
    @include grid100gap(.5em);
  }
}

.farm-head{
  display: flex;
  align-items: center;
  &__title{
    font-size: .875em;
    line-height: 143%;
    font-weight: 500;
    color: $gray;
  }
  &__info{
    width: 1.25em;
    margin-left: .25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__buttons{
    margin-left: .25em;
  }
  &__btn{
    @include center;
    width: .5625em;
    svg{
      width: 100%;
    }
    &._active{
      path{
        fill: $primary;
      }
    }
  }
}


@media screen and (max-width: 1024px){
  .farm-cards{
    grid-template-columns: repeat(2,calc(50% - .75em));
  }
}

@media screen and (max-width: 576px){
  .farm-main{
    grid-template-columns: 100%;
    &__img{
      display: none;
    }
    &__desc,
    &__desc2{
      margin-top: 1em;
    }
  }

  .farm-filter{
    margin-top: 1em;
    &__title{
      display: none;
    }
    &__grid{
      margin: 0;
    }
  }

  .view-toggle{
    display: none;
  }

  .farm-table{
    display: none;
  }

  .farm-cards{
    display: grid;
    grid-template-columns: 100%;
    gap: .75em;
  }
}
