.analytic{
  padding: 5em 0;
  &__top{
    @include center;
  }
  &__title{
    display: none;
  }
  &__links{
    background-color: $dark;
    border: 1px solid rgba(152, 161, 192, 0.10);
    padding: .375em;
    border-radius: 0.5em;
    display: flex;
    align-items: center;
  }
  &__link{
    @include center;
    width: 7.5em;
    height: 1.75em;
    border-radius: 0.25em;
    font-weight: 500;
    color: $gray;
    span{
      font-size: 1em;
      line-height: 120%;
    }
    &._active{
      pointer-events: none;
      color: #fff;
      background: $blue;
    }
  }
}

.analytic-nav{
  margin-top: 1em;
  display: flex;
  .search-field{
    flex-grow: 1;
  }
  .filter-item{
    flex-shrink: 0;
    margin-left: .44em;
  }
}

.filter-item{
  position: relative;
  &__btn{
    width: 3em;
    height: 3em;
    border-radius: 0.5em;
    border: 1px solid rgba(152, 161, 192, 0.10);
    background: $dark;
    &:hover{
      border-color: #fff;
      path{
        stroke: #fff;
      }
    }
    svg{
      width: 1.75em;
      height: auto;
    }
  }
  &._active{
    .filter-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &._type2{
    .filter-item__btn{
      padding: 0;
      border: none;
      width: unset;
      height: unset;
      display: flex;
      align-items: center;
      svg{
        width: 1em;
      }
      &:hover{
        color: #fff;
        path{
          fill: #fff;
        }
      }
    }
  }
}

.filter-dropdown{
  z-index: 5;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  width: 20.375em;
  filter: drop-shadow(4px 4px 16px rgba(0, 0, 0, 0.08));
  background: $bg;
  position: absolute;
  right: -7.25em;
  top: 100%;
  margin-top: 1em;
  padding: 2em 1em .875em;
  border: .0625em solid rgba(152, 161, 192, 0.60);
  border-radius: .5em;
  &__top{
    padding-bottom: .625em;
    border-bottom: 1px solid rgba($gray,.2);
  }
  &__radios{
    margin-top: .875em;
    @include grid100gap(.5em);
  }
  &__switches{
    margin-top: .875em;
    @include grid100gap(.75em);
  }
}

.triangle {
  display: block;
  height: 1.25em;
  width: 1.25em;
  position: absolute;
  top: 0;
  right: 8em;
  z-index: 2;
  transform: translateY(-.625em);
  span{
    width: 100%;
    height: 100%;
    display: block;
    clip-path: polygon(0% 0%, 100% 100%, 0% 100%);
    transform: rotate(135deg);
    border-radius: 0 0 0 0.25em;
    background: $bg;
    background-size: 20.375em 10.1875em;
    border: .0625em solid rgba(152, 161, 192, 0.60);
    filter: drop-shadow(4px 4px 16px rgba(0, 0, 0, 0.08));
  }
}

.analytic-stat{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .25em));
  gap: .5em;
  margin-top: .5em;
}

.chart-card{
  padding: 1.25em 0 1em;
  border-radius: 0.5em;
  background-color: $dark;
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  .analytic__links{
    margin-right: .75em;
  }
  .analytic__link{
    width: auto;
    padding: 0 .5em;
    @include lastM(0 .75em 0 0);
    &._active{
      padding: 0 1.32em;
    }
  }
  .chart{
    width: 100%;
    height: 18em;
  }
}

.apexcharts-tooltip{
  background-color: #272A3C !important;
  border-color: rgba(152, 161, 192, 0.20) !important;
  padding: .75em !important;
  &-title{
    padding: 0 !important;
    background-color: transparent !important;
    border: none !important;
    color: rgba(152, 161, 192, 0.20);
    font-family: $font1 !important;
  }
}

.apexcharts-tooltip-text-y-label,
.apexcharts-tooltip-marker{
  display: none;
}

.apexcharts-tooltip-y-group,
.apexcharts-tooltip-series-group{
  padding: 0 !important;
}

.apexcharts-tooltip-text-y-value{
  margin: 0 !important;
  font-weight: 400;
  text-transform: uppercase;
  font-family: $font1 !important;
  &:first-letter{
    color: rgba(152, 161, 192, 0.20);
  }
}

.apexcharts-grid-row{
  stroke: rgba($gray,.2);
}
.apexcharts-text{
  fill: $gray;
}

.chart-stat{
  padding: 0 1.25em 1em;
  .gray-text{
    opacity: .5;
  }
  &__nums{
    display: flex;
  }
  .status-badge{
    margin-left: .25em;
  }
}

.status-badge{
  display: inline-flex;
  align-items: center;
  height: 1.1875em;
  padding: 0 .25em;
  color: #5CFF94 ;
  background: linear-gradient(91deg, rgba(92, 255, 148, 0.20) 0.63%, rgba(0, 255, 163, 0.20) 94.8%);
  border-radius: .25em;
  span{
    font-size: .75em;
  }
  &._red{
    background: rgba(255, 27, 81, 0.20);
    color: $red;
  }
}

.mob-table__wrap{
  .m-table{
    margin-top: 1em;
  }
  td,
  th{
    text-align: left;
  }
}

.top-tokens,
.top-pairs{
  .pair{
    &__content{
      display: block;
    }
  }
}

.top-tokens{
  margin-top: 3.25em;
  td,
  th{
    &:nth-child(3){
      text-align: center;
    }
  }
}

.top-pairs{
  margin-top: 2.5em;
}

.mob-table{
  th{
    &:first-letter{
      padding-left: .56em;
    }
  }
  &__title{
    display: none;
  }
}

.analytic-table{
  @media screen and (min-width: 577px){
    th,td{
      &:nth-child(1){
        width: 18em;
      }
      &:nth-child(3){
        width: 22em;
      }
      &:nth-child(5){
        width: 9em;
      }
    }
  }
}

.pair{
  display: flex;
  align-items: center;
  .favourite-btn{
    margin-right: .5em;
  }
  &__wrap{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 1.5em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    margin-left: .5em;
    display: flex;
    align-items: center;
  }
  &__items{
    display: flex;
    .pair__img{
      &:not(:first-child){
        margin-left: -.5em;
      }
      &:first-child{
        position: relative;
        z-index: 2;
      }
    }
  }
  .status-badge{
    margin-left: .25em;
  }
}

.favourite-btn{
  &__icon{
    width: 1em;
    @include center;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: auto;
    }
  }
}

.pagination{
  margin-top: 3.25em;
  @include center;
  &__title{
    margin: 0 1.19em;
  }
  &__btn{
    width: .75em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
    &:hover{
      opacity: .8;
    }
    &._disabled{
      opacity: .3;
      pointer-events: none;
    }
  }
}

.top-cryptos{
  padding: 1em 1.5em;
  margin-top: 1em;
  border-radius: 0.5em;
  border: 1px solid rgba(152, 161, 192, 0.10);
  background: $dark;
  display: flex;
  justify-content: space-between;
}

.crypto-item{
  display: flex;
  align-items: center;
  flex-grow: 1;
  &:not(:first-child){
    &:before{
      content: '';
      margin: 0 1em;
      height: 1.71875em;
      width: 1px;
      background: rgba(152, 161, 192, 0.10);
    }
  }
  &__img{
    width: 1.5em;
    margin-right: .5em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__nums{
    display: flex;
    align-items: center;
  }
  .status-badge{
    margin-left: .375em;
  }
}

.coin-top{
  padding: 1em;
  border-radius: 0.5em;
  border: 1px solid rgba(152, 161, 192, 0.10);
  background: $dark;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .pair{
    &__img{
      width: 2.25em;
    }
    &__content{
      margin-left: .25em;
    }
    &__items{
      .pair__img{
        &:not(:first-child){
          margin-left: -1.25em;
        }
      }
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    .m-btn-blue-outline{
      width: 9.125em;
    }
  }
  &__btn{
    @include lastM(0 .5em 0 0);
    span{
      font-size: .875em;
    }
    img{
      width: 2em;
      margin-right: .5em;
    }
  }
}

.analytic-item{
  display: grid;
  grid-template-columns: calc(100% - 21.625em) 21.125em;
  gap: .5em;
  margin-top: 1em;
}

.coin-info{
  border-radius: 0.5em;
  padding: 1.25em;
  background-color: $dark;
  &__top{
    @include grid100gap(.5em);
  }
  &__props{
    margin-top: 2em;
    @include grid100gap(1.25em);
  }
  &__items,
  &__prop{
    @include grid100gap(.25em);
  }
}

.coin-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__nums,
  &__content{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 1.5em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    margin-left: .25em;
  }
  .status-badge{
    margin-left: .5em;
  }
}

.transactions{
  margin-top: 2.5em;
  .filter-item{
    position: absolute;
    top: 1em;
    left: 1em;
    @media screen and (min-width: 577px){
      .filter-dropdown{
        right: unset;
        left: -6.25em;
      }
      .triangle{
        right: unset;
        left: 6.125em;
      }
      @media screen and (max-width: 1700px){
        .filter-dropdown{
          left: -2em;
        }
        .triangle{
          left: 1.75em;
        }
      }
    }
  }
}

.token-amount{
  display: inline-flex;
  align-items: center;
  svg{
    width: .375em;
    height: auto;
    margin-right: .375em;
  }
}


@media screen and (max-width: 1024px){
  .analytic{
    padding: .625em 0 3em;
    &__top{
      justify-content: space-between;
    }
    &__title{
      display: block;
    }
  }

  .analytic-stat{
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .75em;
  }

  .top-cryptos{
    @media screen and (min-width: 577px){
      .crypto-item:nth-child(5)~.crypto-item{
        display: none;
      }
    }
  }

  .coin-top{
    padding-top: .7em;
    padding-bottom: .7em;
    &__btn{
      height: 3em;
    }
  }

  .analytic-item{
    grid-template-columns: calc(100% - 15.3125em) 14.8125em;
  }

  .coin-info{
    &__props{
      margin-top: 1em;
      gap: 1.5em;
    }
  }

  .coin-item{
    @include grid100gap(.5em);
  }

  .filter-dropdown{
    right: 0;
  }

  .triangle{
    right: .75em;
  }
}


@media screen and (max-width: 576px){
  .analytic{
    padding-bottom: 9em;
    &__top{
      display: block;
    }
    &__links{
      width: 100%;
      margin-top: .75em;
    }
  }

  .analytic-stat{
    grid-template-columns: 100%;
  }

  .chart-card{
    padding: .75em 0 0;
    &__top{
      flex-direction: column;
    }
    .analytic__links{
      width: calc(100% - 1.5em);
      order: -1;
      margin: 0 auto 1.25em;
    }
    .analytic__link{
      flex-grow: 1;
    }
  }

  .chart-stat{
    padding: 0 .75em .25em;
  }

  .chart-card{
    .chart{
      width: calc(100% + .25em);
      margin-left: -.25em;
    }
  }

  .mob-table__wrap,
  .top-tokens{
    border-radius: 0.5em;
    border: 1px solid rgba(152, 161, 192, 0.10);
    background-color: $dark;
    padding: 1em;
    .m-table{
      margin-top: .75em;
    }
  }

  .mob-table{
    padding: 0;
    table{
      display: block;
    }
    tbody{
      @include grid100gap(1.25em);
    }
    thead{
      display: none;
    }
    tr{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: 1em .5em;
      &:not(:last-child){
        padding-bottom: 1.25em;
        border-bottom: 1px solid rgba($gray,.2);
      }
    }
    td{
      padding: 0;
      border: none;
      min-height: unset;
      height: unset;
      text-align: left !important;
    }
    &__title{
      display: block;
      font-size: 1em;
      line-height: 119%;
      font-weight: 500;
      color: $gray;
      margin-bottom: .25em;
    }
    &__desc{
      .text14{
        font-size: 1em;
      }
    }
  }

  .transactions,
  .top-pairs,
  .top-tokens{
    td{
      &:nth-child(1){
        grid-column: 1/-1;
        padding-bottom: .5em;
      }
    }
  }

  .pair{
    justify-content: space-between;
    .favourite-btn{
      order: 2;
      margin: 0;
      &__icon{
        width: 1.75em;
      }
    }
  }

  .pagination{
    margin-top: 3em;
  }

  .top-cryptos{
    padding: 1.25em 1em 1.56em;
    @include grid100gap(.875em);
  }

  .crypto-item{
    &:not(:last-child){
      padding-bottom: .875em;
      border-bottom: 1px solid rgba($gray,.2);
    }
    &:before{
      display: none;
    }
    &__content{
      flex-grow: 1;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  .coin-top{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 3;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    border-bottom: none;
    border-left: none;
    border-right: none;
    padding: 1em .75em;
    .pair{
      display: none;
    }
    &__nav{
      display: block;
      width: 100%;
    }
    &__btn{
      width: 100% !important;
      @include lastM(0 0 .5em);
    }
  }

  .analytic-item{
    grid-template-columns: 100%;
    gap: .75em;
  }

  .coin-info{
    order: -1;
    &__props{
      margin-top: 2.125em;
    }
  }

  .coin-info__props,
  .coin-item{
    gap: 1.375em;
    grid-template-columns: repeat(2,calc(50% - .6875em));
  }

  .transactions{
    .filter-item{
      right: 0;
      left: unset;
      top: unset;
      bottom: 100%;
      margin-bottom: .75em;
      .filter-dropdown{
        right: -1em;
      }
      .triangle{
        right: 1.75em;
      }
    }
    td{
      &:nth-child(1){
        padding-bottom: 0em;
      }
    }
  }
}
