.main{
  position: relative;
  margin-top: -7.625em;
  padding: 21em 0 3em;
  min-height: 59em;
  overflow: hidden;
  &__bg{
    pointer-events: none;
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 100%;
    display: block;
    transform: translateX(-50%);
    &._view2{
      display: none;
    }
  }
  &__inner{
    position: relative;
    z-index: 2;
    text-align: center;
    width: 46em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__text{
    @include grid100gap(2.69em);
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2.81em;
  }
  &__btn{
    height: 3em;
    width: 11.5625em;
    border-radius: 0.5em;
  }
  &__float-btn{
    position: absolute;
    padding: 0 2em;
    height: 3em;
    border-radius: 6.25em;
    z-index: 2;
    font-weight: 700;
  }
  ._btn1{
    left: 3.19em;
    top: 18.21em;
    transform: rotate(-11deg);
  }
  ._btn2{
    left: 48%;
    top: 9.46em;
    transform: translateX(-50%) rotate(-11deg);
  }
  ._btn3{
    right: 7.21em;
    top: 30.14em;
    transform: rotate(18deg);
  }
  &__arrow{
    position: absolute;
    &._view2{
      display: none;
    }
  }
  ._arrow1{
    width: 11.75em;
    left: 7.44em;
    top: 24.82em;
    transform: rotate(-167deg);
  }
  ._arrow2{
    width: 10.6875em;
    left: 47%;
    top: 16.96em;
    transform: translateX(-50%) rotate(-67deg);
  }
  ._arrow3{
    width: 16.375em;
    right: 12.38em;
    top: 31.25em;
  }
}

.catalog{
  padding: 12em 0 6em;
  &__grid{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + .5em);
  }
}

.opportunity-card,
.catalog-card{
  overflow: hidden;
  padding: 2em 1.5em 1.5em;
  border-radius: .75em;
  border: $border;
  position: relative;
  &:after,
  &:before{
    content: '';
    position: absolute;
    left: 0;
    height: 0.75em;
    width: 100%;
    background: $card;
    transition: all .3s;
  }
  &:before{
    top: 0;
  }
  &:after{
    bottom: 0;
    background: $blue;
    opacity: 0;
  }
}

.catalog-card{
  background: $radio;
  backdrop-filter: blur(6px);
  width: calc(100% / 3 - .5em);
  margin: .5em .5em 0 0;

  &._no-border{
    &:before,
    &:after{
      display: none;
    }
  }
  &:hover{
    &:after{
      opacity: 1;
    }
    &:before{
      opacity: 0;
    }
  }
  @media screen and (min-width: 577px){
    &._big{
      width: calc(50% - .5em);
    }
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__icon{
    border-radius: .5em;
    width: 2.75em;
    height: 2.75em;
    background: $blue;
    filter: drop-shadow(0px 0px 25px rgba(94, 100, 255, 0.40));
    @include center;
    img{
      width: 2em;
    }
  }
  &__link{
    &:hover{
      opacity: .8;
    }
  }
  &__text{
    margin-top: 1.5em;
    @include grid100gap(.875em);
    .text16{
      hyphens: auto;
    }
  }
  &._pink{
    &:after,
    .catalog-card__icon{
      background: #FC4EEA;
    }
  }
  &._green{
    &:after,
    .catalog-card__icon{
      background: #28E19F;
    }
  }
}

.opportunities{
  padding: 5.6em 0 5.3em;
  background-image: url(../img/line.svg);
  background-repeat: no-repeat;
  background-position: center top 3em;
  background-size: 80em auto;
  &__items{
    @include grid100gap(8.75em);
  }
}

.opportunity-item{
  display: grid;
  grid-template-columns: 29.5625em 24.625em;
  gap: 9.44em;
  position: relative;
  &__content{
    padding-top: 3em;
    position: relative;
    z-index: 2;
  }
  &__text{
    @include grid100gap(1em);
  }
  .text36{
    span{
      display: block;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1em;
  }
  &__img{
    img{
      max-width: 100%;
      display: block;
    }
  }
  &__icon{
    position: absolute;
    left: 24.16em;
    top: -1em;
    width: 11.16em;
  }
  &:nth-child(2){
    .opportunity-item__icon{
      left: 34.81em;
      top: 5.94em;
      width: 13.17em;
    }
  }
  &:nth-child(3){
    .opportunity-item__icon{
      left: -1.25em;
      top: 1.63em;
      width: 14.4375em;
    }
  }
  @media screen and (min-width: 1025px){
    &:nth-child(1){
      gap: 13.25em;
    }
  }
  @media screen and (min-width: 577px){
    &:nth-child(even){
      grid-template-columns: 21.43em 29.5625em;
      gap: 14.19em;
      .opportunity-item__img{
        order: -1;
      }
      .opportunity-item__content{
        padding-top: 3.5em;
      }
    }
  }
}

.how{
  padding: 5.3em 0 6.94em;
  &__top{
    @include grid100gap(1em);
  }
  &__nav{
    display: flex;
  }
  .catalog__grid{
    margin-top: 1.75em;
  }
}

.tabs{
  border-radius: 0.5em;
  padding: .375em;
  border: 1px solid rgba(152, 161, 192, 0.10);
  display: flex;
}

.tab{
  padding: 0 1.13em;
  height: 1.75em;
  display: flex;
  align-items: center;
  font-weight: 500;
  color: $gray;
  border-radius: 0.25em;
  transition: all .3s;
  @include lastM(0 .25em 0 0);
  span{
    font-size: 1em;
  }
  &._active{
    background: $blue;
    color: #fff;
    pointer-events: none;
  }
  &:hover{
    color: #fff;
    cursor: pointer;
  }
}

.tab-content,
.tab-content2{
  display: none;
  &._active{
    display: block;
  }
}

.info{
  padding: 6em 0;
  background-color: #E7E9EF;
  color: $dark;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 30.0625em) 29.5625em;
    gap: 1em .5em;
  }
  ._big{
    grid-column: 1/-1;
  }
}

.info-card{
  border: $border;
  border-radius: 0.75em;
  background: #FFF;
  padding: 2.75em;
  &._blue{
    background: $blue;
    color: #fff;
    .text16{
      color: #fff;
    }
  }
  &__inner{
    @include grid100gap(1em);
  }
  &__nav{
    display: flex;
  }
}

.roadmap{
  padding: 7.19em 0 4.7em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.32em / 4));
    gap: .44em;
    margin-top: 3.31em;
  }
}

.roadmap-step{
  position: relative;
  &:not(:last-child){
    &:after{
      content: '';
      position: absolute;
      width: 4.0625em;
      height: 2em;
      background-image: url(../img/step-arrow.svg);
      @include bgImgSize(100%);
      top: 1.25em;
      left: 100%;
      margin-left: -4.5em;
    }
  }
  &:not(._active-arrow){
    &:after{
      opacity: .3;
    }
  }
  &__top{
    width: 11.875em;
    max-width: 100%;
    position: relative;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    position: absolute;
    padding: 1.19em 0;
    text-align: center;
    width: 100%;
    top: 0;
    left: 0;
  }
  &__items{
    margin-top: 2.19em;
  }
  &:not(._current){
    color: $gray;
    .roadmap-step__top{
      img{
        opacity: .3;
      }
    }
    .roadmap-step__items{
      li{
        &:before{
          background-image: url(../img/list-arrow2.svg);
        }
      }
    }
  }
}

.team{
  padding: 4.7em 0 3.1em;
  .catalog__grid{
    margin-top: 1em;
  }
}

.worker{
  display: flex;
  align-items: center;
  &__img{
    width: 4em;
    height: 4em;
    flex-shrink: 0;
    margin-right: .75em;
    img{
      border-radius: .75em;
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  &__info{
    @include grid100gap(.25em);
  }
}


@media screen and (min-width: 1700px){
  .main{
    &__inner{
      width: 54em;
    }
    &__text{
      gap: .44em;
    }
    ._btn1 {
      left: 18.19em;
    }
    ._btn3{
      right: 22em;
    }
    ._arrow1{
      top: 22em;
      left: 23.5em;
    }
    ._arrow3{
      right: 27em;
    }
  }

  .text64{
    font-size: 5em;
  }

  .catalog-card{
    .text16{
      font-size: 1.25em;
    }
  }

  .how{
    padding-top: 6.8em;
    padding-bottom: 9.69em;
  }

  .info{
    padding: 8.13em 0 9.31em;
  }

  .roadmap{
    padding: 11.87em 0 6.25em;
  }

  .team{
    padding: 6.25em 0 6.45em;
  }
}


@media screen and (max-width: 1920px){
  .main{
    &__bg{
      width: 120em;
    }
  }
}


@media screen and (max-width: 1024px){
  .main{
    ._btn1{
      left: 2.81em;
    }
    ._btn3{
      right: 2.8125em;
    }
    ._arrow1{
      left: 6em;
    }
    ._arrow3{
      right: 7.375em;
    }
  }

  .catalog{
    padding-bottom: 3.4em;
  }

  .opportunities{
    padding: 3.44em 0 4.125em;
    &__items{
      gap: 7.3em;
    }
  }

  .opportunity-item{
    gap: 5.5em;
    &__icon{
      left: 19.87em;
      top: -.34em;
    }
    &:nth-child(2){
      .opportunity-item__icon{
        left: 29.87em;
      }
    }
    @media screen and (min-width: 577px){
      &:nth-child(even){
        gap: 9em;
      }
    }
  }

  .how{
    padding: 4.125em 0 7.5em;
    @media screen and (min-width: 577px){
      .catalog__grid{
        width: calc(100% + .375em);
      }
      .catalog-card{
        width: calc(100% / 3 - .375em);
        margin-right: .375em;
      }
    }
  }

  .info{
    padding: 3.44em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .25em));
    }
  }

  .roadmap{
    padding: 5.31em 0 4.37em;
    &__grid{
      margin-top: 2.69em;
    }
  }

  .team{
    padding: 4.38em 0 3.935em;
  }
}


@media screen and (max-width: 576px){
  .main{
    margin-top: -4.5em;
    padding-top: 18em;
    &__bg{
      width: 23.4375em;
      bottom: unset;
      top: 0;
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    &__text{
      gap: .88em;
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      width: 100%;
    }
    &__float-btn{
      padding: 0 1.5em;
      span{
        font-size: .75em;
      }
    }
    &__arrow{
      position: absolute;
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    ._btn2{
      top: 10.43em;
      left: .47em;
      transform: rotate(-11deg);
    }
    ._arrow2{
      width: 5.3125em;
      top: 15.71em;
      left: 3em;
      transform: rotate(-98.5deg);
    }
    ._btn3{
      right: 0;
      top: 10.33em;
    }
    ._arrow3{
      width: 6em;
      top: 15.28em;
      right: 3.76em;
      transform: rotate(-40.5deg);
    }
    ._btn1{
      right: 5.5em;
      left: unset;
      top: unset;
      bottom: 15.5em;
      transform: rotate(-20.5deg);
      padding: 0 1.25em;
    }
    ._arrow1{
      transform: rotate(68deg);
      right: 8.625em;
      left: unset;
      top: unset;
      bottom: 20.5em;
      width: 5.25em;
    }
  }

  .catalog{
    margin-top: -1em;
    padding: 0 0 4em;
  }

  .catalog-card{
    width: calc(100% - .5em);
    &__info{
      gap: .5em;
    }
  }

  .opportunities{
    overflow: hidden;
    padding: 4.06em 0 3.8em;
    &__items{
      gap: 5em;
    }
  }

  .opportunity-item{
    grid-template-columns: 100%;
    gap: 2em;
    &:nth-child(1){
      .opportunity-item__icon{
        width: 10.125em;
        left: unset;
        right: -2.875em;
        top: 9em;
        transform: rotate(103.5deg);
      }
    }
    &:nth-child(2){
      .opportunity-item__img{
        width: 19.5em;
        max-width: 100%;
        margin: 0 auto;
      }
      .opportunity-item__icon{
        width: 7.6875em;
        left: -.75em;
        top: 1.75em;
      }
    }
    &:nth-child(3){
      .opportunity-item__img{
        width: 17.9375em;
        max-width: 100%;
        margin: 0 auto;
      }
      .opportunity-item__icon{
        top: -1.37em;
      }
    }
    &__content{
      padding: 0;
    }
  }

  .how{
    display: none;
  }

  .info{
    padding: 4.38em 0 4.81em;
    &__grid{
      grid-template-columns: 100%;
      gap: .75em;
    }
  }

  .info-card{
    padding: 1em;
  }

  .roadmap{
    padding: 8.13em 0 4.06em;
    background-image: url(../img/rodmap-bg2.svg);
    background-size: cover;
    .text36{
      text-align: center;
    }
    &__grid{
      margin-top: 2.81em;
      padding: 0 2em;
      gap: 5.69em;
      grid-template-columns: 100%;
    }
  }

  .roadmap-step{
    display: flex;
    flex-direction: column;
    align-items: center;
    &:not(:last-child){
      &:after{
        width: 4.0625em;
        height: 2em;
        background-image: url(../img/step-arrow.svg);
        top: 100%;
        left: 50%;
        transform: translateX(-50%);
        margin: 0;
      }
    }
    &__top{
      margin-left: auto;
      margin-right: auto;
    }
  }

  .team{
    padding: 4.065em 0 4.51em;
    .catalog__grid{
      width: 100%;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
    }
    .catalog-card{
      padding: 1.5em 1em 1em;
      margin: 0;
      width: 100%;
      &__text{
        margin-top: .75em;
      }
    }
  }

  .worker{
    &__img{
      width: 3.25em;
      height: 3.25em;
      margin-right: .5em;
    }
  }
}

