.pool-row{
  border-radius: .75em;
  background-color: $dark;
  &._active{
    .pool-row__toggle svg{
      transform: rotate(-180deg);
    }
  }
  &__top{
    padding-top: 1em;
    padding-bottom: 1em;
    align-items: center;
  }
  &__bottom{
    padding: .75em 1.5em 1em;
  }
  &__action{
    display: flex;
    justify-content: flex-end;
  }
  &__toggle{
    width: 1.5em;
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
}

.pool-info{
  .pairs{
    margin-right: .5em;
  }
}

.pool-apr{
  img{
    width: 1em;
    margin-left: .125em;
  }
}

.pool-hint{
  margin-left: 1.25em;
  position: relative;
  &:hover{
    .pool-hint__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__dropdown{
    position: absolute;
    left: 50%;
    bottom: 100%;
    margin-bottom: -.25em;
    transform: translateX(-50%);
    width: 13.3125em;
    padding: 1em;
    border: 1px solid rgba($gray,.6);
    background: $bg;
    border-radius: .75em;
    @include grid100gap(1.25em);
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    img{
      width: 1.25em;
    }
  }
  &__btn{
    width: 1.25em;
    @include center;
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
}

.pool-detail{
  padding: 1em 1.5em;
  border: 1px solid #1F2334;
  border-radius: 1em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__btn{
    width: 9.375em;
    img{
      width: 1.25em;
      margin-right: .5em;
    }
  }
  &__info{
    color: rgba($gray,.5);
    @include grid100gap(.125em);
  }
  &__arrow-btn{
    width: 1.5em;
    @include center;
    svg{
      width: 100%;
    }
  }
  &__calc{
    display: flex;
    &-btn{
      width: 7.5em;
      margin-right: .75em;
      &:last-child{
        margin-right: 0;
      }
    }
  }
}

._hover-blue{
  &:hover{
    path{
      stroke: $primary;
    }
  }
}
