.reserve-info,
.balance-info{
  padding-bottom: 1.25em;
  @include grid100gap(1.25em);
  border-bottom: 1px solid rgba(152, 161, 192, 0.10);
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: .625em 1em;
  }
}

.balance-info{
  .reserve-info__items{
    grid-template-columns: 100%;
  }
}

.manft-select{
  height: unset;
  .select-dropdown{
    padding: .625em 1em;
  }
  .m-table{
    padding: 0;
    th,
    td{
      padding: .375em 0;
      height: unset;
      border: none;
      &:last-child{
        text-align: right;
      }
    }
  }
}

.error-message{
  padding: .75em 1em;
  border-radius: 1em;
  border: 1px solid $red;
  background: rgba($red, 0.80);
  @include grid100gap(.25em);
  &__top{
    display: flex;
    align-items: center;
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
}


@media screen and (max-width: 576px){
  .liquidity-polls{
    margin-top: .75em;
    &__title{
      display: none;
    }
  }

  .liquidity-table{
    margin-top: 0;
    th,td{
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4),
      &:nth-child(6),
      &:nth-child(8){
        display: none;
      }
    }
  }
}
