.team-page{
  background-image: url(../img/team-bg.svg);
}

.team2{
  padding: 3em 0;
}

.team-main{
  align-items: center;
  grid-template-columns: auto 21.5625em;
}

.values{
  padding: 3.5em 0 8.75em;
  background: $gradietn;
  margin-top: 2em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 2.5em / 6));
    gap: .5em;
    margin-top: 3.25em;
  }
}

.value-card{
  background-color: rgba(#fff,.1);
  border: 1px solid rgba(#fff,.3);
  border-radius: .75em;
  padding: 2em 1.5em 1.5em;
  position: relative;
  overflow: hidden;
  @media screen and (min-width: 577px){
    grid-column: span 2;
    &._big{
      grid-column: span 3;
    }
  }
  &:before{
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    height: 0.75em;
    width: 100%;
    background: rgba(#fff,.3);
    transition: all .3s;
  }
  &__icon{
    width: 2.75em;
    height: 2.75em;
    box-shadow: 0 0 1.5625em -.0625em rgba(94, 100, 255, 0.4);
    @include center;
    border-radius: .75em;
    background-color: #fff;
    img{
      width: 2em;
    }
  }
  &__text{
    margin-top: 1.5em;
    @include grid100gap(.875em);
  }
}


@media screen and (max-width: 1024px){
  .team-page{
    background-image: url(../img/team-bg2.svg);
  }

  .team2{
    padding: 1em 0 5em;
  }

  .values{
    padding-bottom: 4.375em;
    &__grid{
      margin-top: 2.25em;
    }
  }
}

@media screen and (max-width: 576px){
  .team-page{
    background-image: url(../img/team-bg3.svg);
    background-position: left 0 top 15em;
  }

  .team2{
    padding: 7em 0 4.375em;
  }

  .team-main{
    text-align: center;
    grid-template-columns: 100%;
    gap: 1.25em;
    .farm-main__img{
      display: block;
      width: 18em;
      margin: 0 auto;
    }
  }

  .team3{
    padding: 4.375em 0;
    .text36{
      text-align: center;
    }
    .catalog__grid{
      grid-template-columns: 100%;
    }
  }

  .values{
    margin-top: 3.75em;
    padding: 6em 0;
    &__grid{
      margin-top: 1em;
      grid-template-columns: 100%;
    }
  }
}
