.launchpools-main{
  align-items: center;
}

.token-page,
.team-page,
.launchpools-page{
  background-image: url(../img/launchpools-bg.svg);
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: 0 0;
}

.launchpools-table{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 53.5em) 9em 8em 10em 10em 16.5em;
    padding-left: 1.5em;
    padding-right: 1.5em;
  }
  .pool-row{
    background-image: url(../img/mark.svg);
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 1.25em auto;
    &._blue{
      background-image: url(../img/mark2.svg);
    }
    &._pink{
      background-image: url(../img/pink.svg);
    }
    &__action{
      display: grid;
      grid-template-columns: calc(100% - 2.25em) 1.5em;
      gap: .75em;
      align-items: center;
    }
    &__btn{
      width: 100%;
      height: 1.75em;
      border-radius: .25em;
      span{
        font-size: .75em;
      }
      img{
        width: 1.25em;
        margin-right: .25em;
      }
    }
  }
}

.pool-detail{
  &__col{
    flex-grow: 1;
    max-width: 47.375em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &._small{
      max-width: 34em;
    }
  }
  &__col2{
    width: 15em;
    flex-shrink: 0;
  }
  &__contract,
  &__metamask{
    display: flex;
    align-items: center;
    color: $primary;
    img{
      width: 1.375em;
      margin-left: .75em;
    }
    &:hover{
      opacity: .7;
    }
  }
  &__contract{
    img{
      width: 1.25em;
    }
  }
  &__calc2{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .75em;
    .pool-detail__calc-btn{
      width: 100%;
      height: 1.75em;
      span{
        font-size: .75em;
      }
    }
  }
  &__nav{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: .5em;
    margin-top: .75em;
  }
  &__btn2{
    border-radius: .25em;
    width: 100%;
    height: 1.75em;
    span{
      font-size: .75em;
    }
  }
}

.pool-info2{
  &__icon{
    width: 1.75em;
    margin-right: .5em;
    filter: drop-shadow(0 .25em .25em rgba(0, 0, 0, 0.25));
  }
  &__content{
    @include grid100gap(.125em);
  }
}

.launchpool-card{
  position: relative;
  .pool-info2{
    .gray-text{
      color: #fff;
    }
  }
  &._light-blue{
    .farm-card__top {
      background: #6E81FF;
    }
  }
  &._pink{
    .farm-card__top {
      background: #CB93FD;
    }
    .pool-info2{
      .gray-text{
        color: #fff;
      }
    }
  }
  &__coin{
    position: absolute;
    top: 0;
    right: .5em;
    z-index: 3;
    width: 6.5625em;
  }
  .farm-card__top{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url(../img/rects.svg);
      background-size: 100% auto;
      background-repeat: no-repeat;
    }
  }
  .pool-info2__icon{
    filter: unset;
  }
}

.hint-wrap{
  display: flex;
  align-items: center;
  .hint-block{
    margin-left: .25em;
  }
  &__btn{
    width: 1.25em;
    @include center;
    svg{
      width: 100%;
    }
  }
}

.hint-block{
  position: relative;
  &:hover{
    .hint-wrap__btn{
      path{
        stroke: #fff;
      }
    }
    .hint-block__wrap{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__wrap{
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
    margin-bottom: -1px;
    padding-bottom: .5em;
    z-index: 5;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__inner{
    width: 20.625em;
    padding: 1em;
    background: $bg;
    border: 1px solid rgba($gray,.6);
    border-radius: .75em;
  }
}

.launchpool-merge{
  &._center .withdraw-field{
    input{
      padding: .42em;
      text-align: center;
    }
  }
}

.icon-btn{
  @include center;
  &:hover{
    cursor: pointer;
    opacity: .7;
  }
}


@media screen and (max-width: 1024px){
  .launchpools-page{
    background-image: url(../img/launchpools-bg2.svg);
  }

  .launchpools-table {
    &__grid {
      grid-template-columns: calc(100% - 44.25em) 6.75em 6em 7.5em 7.5em 16.5em;
    }
  }

  .pool-detail{
    &__col{
      max-width: 43em;
      &._small{
        max-width: 33em;
      }
    }
    &__col2:not(._big){
      width: 9.25em;
    }
  }
}

@media screen and (max-width: 576px){
  .launchpools-page{
    background-image: unset;
  }

  .hint-block{
    &__wrap{
      display: none;
    }
  }
}
