.header{
  padding: 1.5em 0;
  position: relative;
  z-index: 10;
  @media screen and (min-width: 1025px){
    .container{
      padding: 0 2em;
    }
    .theme-switch{
      display: none;
    }
    .header__inner{
      border-bottom: none;
    }
  }
  &__inner{
    display: flex;
    align-items: center;
    padding-bottom: 1.25em;
    border-bottom: 1px solid rgba(152, 161, 192, 0.15);
  }
  &__logo{
    display: block;
    width: 10.5em;
    flex-shrink: 0;
    margin-right: 3.875em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}

.menu{
  display: flex;
  align-items: center;
  .theme-switch{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    color: $gray;
    font-size: 1em;
    line-height: 119%;
    @include lastM(0 1.5em 0 0);
    &:hover{
      color: $textColor;
    }
    &._active{
      height: 2.1875em;
      padding: 0 .75em;
      border-radius: 0.5em;
      background: rgba(152, 161, 192, 0.10);
      @include center;
      pointer-events: none;
    }
  }
}

.header-action{
  display: flex;
  align-items: center;
  margin-left: auto;
  &__btn{
    width: 11.5625em;
    height: 2.5em;
    border-radius: 0.5em;
    img{
      width: 2em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  .m-btn-blue-outline{
    img{
      width: 1.5em;
    }
  }
}

.m-page._light{
  .theme-switch{
    ._view1{
      opacity: 0;
      pointer-events: none;
    }
    ._view2{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.theme-switch{
  margin-right: .75em;
  width: 2.5em;
  height: 2.5em;
  position: relative;
  &__btn{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 0.5em;
    border: 1px solid rgba(152, 161, 192, 0.20);
  }
  svg{
    @include centerAbsolute;
    width: 1.5em;
    height: auto;
  }
  ._view2{
    opacity: 0;
    pointer-events: none;
  }
  &:hover{
    border-color: #fff;
    path{
      stroke: #fff;
    }
  }
}

.burger{
  display: none;
}

.header-socials{
  display: flex;
  align-items: center;
  margin-left: 1.5em;
  &__item{
    width: 1.3125em;
    @include center;
    margin-right: .75em;
    &:last-child{
      margin-right: 0;
    }
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path:not(._stroke){
        fill: #fff;
      }
      ._stroke{
        stroke: #fff;
      }
    }
  }
}


@media screen and (max-width: 1024px){
  .header{
    padding: 1.25em 0;
    &__logo{
      z-index: 21;
      position: relative;
      margin-right: 1em;
      width: 2.375em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &._open{
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .menu{
        transform: translateX(0%);
      }
    }
  }

  .burger{
    background-color: transparent;
    z-index: 21;
    display: flex;
    margin-left: .75em;
    width: 2.5em;
    height: 2.5em;
    position: relative;
    svg{
      @include centerAbsolute;
      width: 2.5em;
      height: auto;
    }
    ._view2{
      width: 2em;
      opacity: 0;
      path{
        stroke-width: .5px;
      }
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $fullHeight;
    z-index: 20;
    background: $bg;
    padding: 6em 1.625em 2em;
    overflow-y: auto;
    transition: all .3s;
    transform: translateX(-120%);
    flex-direction: column;
  }

  .nav{
    flex-direction: column;
    &__link{
      font-size: 1.75em;
      @include lastM(0 0 .85em);
      &._active{
        height: 1.78em;
        padding: 0 .43em;
        border-radius: .29em;
      }
    }
  }

  .header-socials{
    margin: 2em 0 0;
    font-size: 1.75em;
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: .625em 0;
    &__inner{
      border: none;
      padding: 0;
      position: relative;
    }
  }

  .burger{
    position: absolute;
    left: 2.875em;
    top: 50%;
    transform: translateY(-50%);
    margin-left: 0;
  }

  .theme-switch{
    display: none;
  }

  .menu{
    padding: 5em .75em 2em;
    .theme-switch{
      margin-top: 2.5em;
      width: auto;
      height: auto;
      @include center;
      &__btn{
        opacity: 1 !important;
        pointer-events: unset !important;
        position: relative;
        width: 3em;
        height: 3em;
        padding: 0;
        margin: 0 .375em;
        @include center;
        &._view1{

        }
      }
    }
  }

  .m-page._light{
    .theme-switch{
      ._view1{
        background: $gray;
        path{
          fill: $dark;
          stroke: $dark;
        }
      }
    }
  }

  .m-page:not(._light){
    .theme-switch ._view2{
      background: $gray;
      path{
        fill: $dark;
        stroke: $dark;
      }
    }
  }
}
